<template>
  <div>
    <Header></Header>
    <div class="main">
      <el-row :gutter="20">
        <el-col :span="16">
          <el-carousel height="400px">
            <el-carousel-item v-for="item in carouselList" :key="item">
              <img style="width: 100%" :src="item" />
            </el-carousel-item>
          </el-carousel>
        </el-col>
        <el-col :span="8">
          <el-calendar id="index_calendar" v-model="nowDate"> </el-calendar>
        </el-col>
      </el-row>
      <div class="separate"></div>
      <el-row :gutter="20">
        <el-col :span="18">
          <div class="article-list">
            <p>最新文章</p>
            <div
              class="article-node"
              v-for="value in article.recent"
              :key="value.id"
            >
              <div class="article-left">
                <router-link target="_blank" :to="'/article/' + value.id"
                  ><img :src="value.firstImg"
                /></router-link>
              </div>
              <div class="article-right">
                <p class="article-title">
                  <router-link target="_blank" :to="'/article/' + value.id">{{
                    value.title
                  }}</router-link>
                </p>
                <p class="article-tag">
                  <el-tag v-for="tag in value.tagList" :key="tag.id" type="info" size="mini"><router-link target="_blank" :to="{path:'/tag/'+ tag.label,query:{id:tag.id}}">{{tag.label}}</router-link></el-tag>
                </p>
                <p class="article-describe">
                  <router-link target="_blank" :to="'/article/' + value.id"
                    ><span
                      >{{value.content}}
                    </span></router-link
                  >
                </p>
                <p class="article-other">
                  <span
                    ><router-link target="_blank" :to="'/user/' + value.authorId"
                      >{{ value.authorUsername }}</router-link
                    ></span
                  >
                  <span>{{value.publishTime.split("T")[0]}}</span>
                  <span><i class="el-icon-view"></i>&nbsp;&nbsp;{{value.views}}</span>
                </p>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="article-list">
            <p>精选文章</p>
            <div class="special-area">
              <div class="special-node" v-for="value in article.special" :key="value.specialId">
                <p class="special-title">
                  <router-link target="_blank" :to="'/article/' + value.specialId"
                    >{{value.specialTitle}}</router-link
                  >
                </p>
                <p class="special-other">
                  <span
                    ><img :src="value.authorAvatar === undefined ? userImg : value.authorAvatar" />&nbsp;<router-link
                      target="_blank"
                      :to="'/user/'+value.authorId"
                      >{{value.authorUsername}}</router-link
                    ></span
                  >
                  <span><i class="el-icon-view"></i>&nbsp;{{value.views}}</span>
                </p>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="18">
          <div class="article-list">
            <p>最新问答</p>
            <div class="answer-node" v-for="value in question.recent" :key="value.id">
              <p class="answer-title">
                <router-link target="_blank" :to="'/question/' + value.id"
                  >{{value.title}}</router-link
                >
              </p>
              <p class="answer-describe">
                <router-link target="_blank" :to="'/question/' + value.id"
                  >{{value.content}}
                </router-link>
              </p>
              <p class="answer-other">
                <span
                  ><router-link target="_blank" :to="'/user/' + value.authorId"
                    >{{value.authorUsername}}</router-link
                  ></span
                >
                <span>{{value.publishTime.split("T")[0]}}</span>
                <span><i class="el-icon-view"></i>&nbsp;&nbsp;{{value.views}}</span>
              </p>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="article-list">
            <p>精选问答</p>
            <div class="answer-area">
              <div class="special-node" v-for="value in question.special" :key="value.specialId">
                <p class="special-title">
                  <router-link target="_blank" :to="'/question/' + value.specialId"
                    >{{value.specialTitle}}</router-link
                  >
                </p>
                <p class="special-other">
                  <span
                    ><img :src="value.authorAvatar === undefined ? userImg : value.authorAvatar" />&nbsp;<router-link
                      target="_blank"
                      :to="'/user/'+ value.authorId"
                      >{{value.authorUsername}}</router-link
                    ></span
                  >
                  <span><i class="el-icon-view"></i>&nbsp;{{value.views}}</span>
                </p>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import common from "@/api/common";

export default {
  name: "Index",
  data() {
    return {
      nowDate: new Date(),
      userImg: require("@/assets/images/nologin.png"),
      carouselList: [
        require("@/assets/images/1.png"),
        require("@/assets/images/2.png"),
        require("@/assets/images/3.png"),
      ],
      article: {
        recent: [],
        special: [],
      },
      question: {
        recent: [],
        special: [],
      },
    };
  },
  mounted() {
    this.getIndexArticle();
    this.getIndexQuestion();
  },
  methods: {
    getIndexArticle() {
      common.getIndexArticle().then((resp) => {
        let data = resp.data.data;
        this.article.recent = data.recent;
        this.article.special = data.special;
      });
    },
    getIndexQuestion() {
      common.getIndexQuestion().then((resp) => {
        let data = resp.data.data;
        this.question.recent = data.recent;
        this.question.special = data.special;
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}
/* 整个首页的主体 */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
  /* background-color: rgb(203, 203, 128); */
  /* z-index: 1; */
}

/* 定义日历框架的高度 */
#index_calendar {
  height: 400px;
}

/* 深度修改：修改日历格子的高度 */
div /deep/ .el-calendar-table .el-calendar-day {
  height: 40px;
}

/* 隔开每一个部分,如：文章部分与问答部分与公告栏部分 */
.separate {
  height: 30px;
}

/* 文章标题 底部样式 */
.article-list p {
  margin-bottom: 18px;
}

/* 最新文章(一栏一栏) 主体样式 */
.article-node {
  display: flex;
  width: 100%;
  height: 200px;
  /* border: 1px solid black; */
  background-color: white;
  margin-bottom: 15px;
}

/* 精选文章(一大列) 主体样式*/
.special-area {
  height: 630px;
  width: 100%;
  background-color: #ffffff;
}

/* 最新文章/一栏/左边部分 图片部分 */
.article-left {
  display: inline-block;
  width: 23%;
  height: 100%;
  /* background-color: yellow; */
  text-align: center;
  /* border-right: 1px solid rgb(74, 74, 118); */
}

.article-left img {
  width: 85%;
  height: 85%;
  margin-top: 15px;
}

/* 最新文章/一栏/右边部分 显示部分*/
.article-right {
  display: inline-block;
  width: 77%;
  height: 100%;
  /* background-color: yellow; */
}

/* 标题 */
.article-right .article-title {
  margin-top: 18px;
  margin-left: 13px;
  font-size: 21px;
  text-overflow: ellipsis;
}

.article-right .article-title a:hover,
.answer-title a:hover,
.special-title a:hover {
  color: rgb(62, 175, 124);
}

/* 标签 */
.article-right .article-tag {
  margin-left: 28px;
  margin-top: -7px;
}

.article-right .article-tag .el-tag {
  margin-right: 5px;
}

/* 描述 */
.article-right .article-describe {
  width: 95%;
  height: 60px;
  margin-left: 13px;
  font-size: 14px;
  color: gray;

  margin-top: -8px;
}

.article-right .article-describe span {
  /* 多行文字过长自动省略 */
  word-break: break-all;
  /* 以下五行实现多行省略 */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.article-right .article-describe a {
  color: gray;
}

/* 其他 */
.article-right .article-other {
  margin-left: 13px;
  color: black;
}

.article-right .article-other span {
  display: inline-block;
  width: 100px;
  margin-right: 20px;
  font-size: 14px;
  border-right: 1px solid grey;
}

.article-right .article-other span:last-child {
  border-right: 0px;
}

/* 精选文章/一栏 主体 */
.special-node {
  height: 105px;
  width: 100%;
  /* background-color: yellow; */
}

/* 标题 */
.special-title {
  padding-top: 18px;
  margin-left: 13px;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 其他内容 */
.special-other span {
  margin-left: 13px;
  display: inline-block;
  font-size: 13px;
}

.special-other span img {
  width: 30px;
  height: 30px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
  border-radius: 25px;
}

.special-other span:last-child {
  width: 20%;
  float: right;
  /* background-color: yellow; */
  margin-top: 5px;
}

/* 精选文章(一大列) 主体样式*/
.answer-area {
  height: 420px;
  width: 100%;
  background-color: #ffffff;
}

/* 最新问答/一栏 主体样式 */
.answer-node {
  display: inline-block;
  width: 49%;
  height: 199px;
  background-color: white;
  margin-right: 8px;
  margin-bottom: 20px;
}

.answer-title {
  margin-top: 13px;
  margin-left: 13px;
  font-size: 21px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.answer-describe {
  width: 95%;
  height: 60px;
  margin-left: 13px;
  /* font-size: 14px; */
  color: gray;

  /* 多行文字过长自动省略 */
  word-break: break-all;
  /* 以下五行实现多行省略 */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-top: -8px;
}

.answer-describe a {
  color: gray;
}

.answer-node {
  position: relative;
}

.answer-other {
  position: absolute;
  bottom: 0px;
}

.answer-other span {
  bottom: 0;
  margin-left: 13px;
  display: inline-block;
  width: 100px;
  margin-right: 20px;
  font-size: 14px;
  border-right: 1px solid grey;
}

.answer-other span:last-child {
  border-right: 0px;
}
</style>