<template>
  <div>
    <div class="header">
      <el-row>
        <el-col :span="5"
          ><div class="header_logo">
            <router-link to="/">AATTS</router-link>
          </div></el-col
        >
      </el-row>
    </div>
    <div class="main_body">
      <div class="main">
        <el-row>
          <el-col :span="16">&nbsp;</el-col>
          <el-col :span="8">
            <div class="login_body">
              <div class="login_tip">
                <el-row>
                  <el-col
                    style="border-right: 1px solid rgb(230, 230, 230)"
                    :span="12"
                    ><router-link to="/login">登录</router-link></el-col
                  >
                  <el-col :span="12"
                    ><router-link to="/register"
                      ><span class="active">注册</span></router-link
                    ></el-col
                  >
                </el-row>
              </div>
              <div class="login_input">
                <div class="item1">
                  <el-input
                    prefix-icon="fa-solid fa-user"
                    v-model="form.username"
                    placeholder="请输入要注册的用户名"
                  ></el-input>
                </div>
                <div class="item1">
                  <el-input
                    prefix-icon="fa-solid fa-lock"
                    v-model="form.password"
                    placeholder="请输入要注册的密码"
                  ></el-input>
                </div>
                <div class="item1">
                  <el-input
                    prefix-icon="fa-solid fa-envelope"
                    v-model="form.email"
                    placeholder="请输入邮箱"
                  ></el-input>
                </div>
                <div class="item2">
                  <el-input
                    prefix-icon="fa-solid fa-repeat"
                    v-model="form.code"
                    placeholder="请输入邮箱验证码"
                  ></el-input>
                  <el-button
                    @click="sendCodeClick()"
                    :disabled="sendCodeStatus"
                    plain
                    >{{ sendCode }}</el-button
                  >
                </div>
              </div>
              <div class="login_submit">
                <el-button @click="register" plain>注册并登陆</el-button>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import common from "@/api/common";
import cookie from 'js-cookie';

export default {
  name: "Login",
  data() {
    return {
      form: { username: "", password: "", code: "", email: "" },
      imgUrl: "",
      sendCode: "发送验证码",
      sendCodeStatus: false,
      second: 300,
    };
  },
  beforeMount() {
    this.getVerifyCode();
  },
  mounted() {},
  methods: {
    getVerifyCode() {
      common.getVerifyCode().then((resp) => {
        this.imgUrl = resp.data;
      });
    },
    register() {
      //TODO 注册功能
      common.register(this.form).then((resp) => {
        if (resp.data.code === 200) {
          let token = resp.headers.get("accessKey");
          //{domain:"localhost"}：限定作用范围
          cookie.set("token", token, { domain: "localhost" });
          this.$bus.$emit("loginRefresh"); //登陆成功 触发登陆刷新header事件
          this.$router.push("/");
        } else {
          this.$message({
            message: resp.data.message,
            type: "error",
          });
        }
      });
    },
    sendCodeClick() {
      //TODO 发送验证码到邮箱动能
      if (this.form.email.length === 0) {
        this.$message({
          message: "请先填写邮箱再点击发送...",
          type: "error",
        });
        return;
      }

      common.sendCode(this.form.email);

      //验证码发送框
      this.sendCodeStatus = true;
      this.sendCode = `${this.second}s`;

      let timer = setInterval(() => {
        //需要定时执行的代码
        if (this.second < 1) {
          this.sendCode = "发送验证码";
          this.sendCodeStatus = false;
          clearInterval(timer);
          return;
        } else {
          this.second = this.second - 1;
        }
        this.sendCode = `${this.second}s`;
      }, 1000);

      this.$message({
        message: "发送成功",
        type: "success",
      });
    },
  },
};
</script>

<style scoped>
/* 头部样式 */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  background-color: white;
  /* border-bottom: 1px solid rgb(167, 157, 157); */
  box-shadow: 0 0 5px rgb(181, 179, 179);
  z-index: 20;
}

.header_logo {
  /* line-height: 60px; */
  text-align: center;
  display: inline-block;
  width: 200px;
  height: 100%;
  /* background-color: grey; */
}

.main_body {
  height: 550px;
  background-image: linear-gradient(45deg, #aab4e3, rgb(125, 204, 169));
}

/* 登录页主体样式 */
.main {
  margin: 0 auto;
  margin-top: 60px;
  width: 1200px;
  /* background-color: rgb(203, 203, 128); */
  /* z-index: 1; */
}

/* 登录框主体 */
.login_body {
  width: 100%;
  height: 400px;
  background-color: white;
}

/* 登录提示框主体 */
.login_tip {
  width: 100%;
  height: 80px;
  /* background-color: rgb(114, 177, 114); */
  text-align: center;
  border-bottom: 1px solid rgb(230, 230, 230);
}

.login_tip a {
  text-decoration: none;
  color: black;
  line-height: 80px;
  font-size: 21px;
  color: grey;
}

.login_tip a:hover,
.active {
  color: rgb(62, 175, 124);
}

.login_input {
  width: 100%;
  height: 260px;
  /* background-color: rgb(172, 220, 172); */
  text-align: center;
}

.item1 .el-input {
  width: 80%;
  margin-top: 20px;
}

.item2 {
  margin-top: 20px;
}

.item2 .el-input {
  width: 40%;
}

.item2 .el-button {
  vertical-align: middle;
  width: 35%;
  height: 40px;
  margin-left: 20px;
}

.login_submit {
  width: 100%;
  text-align: center;
}

.login_submit .el-button {
  width: 320px;
  background-color: rgb(62, 175, 124);
  color: white;
}

.el-row {
  margin-top: 20px;
}
</style>