<template>
  <div>
    <el-dialog title="个人资料" :visible.sync="userDialogVisible" width="30%">
      <div class="user_info_item">
        <span>头像：</span>
        <el-upload
          class="avatar-uploader"
          action="https://kkc-picture.oss-cn-shenzhen.aliyuncs.com"
          :data="uploadData"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="userInfo.avatar" :src="userInfo.avatar" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>
      <div class="user_info_item">
        <span>用户id：</span>
        <span>{{ userInfo.id }}</span>
      </div>
      <div class="user_info_item">
        <span>用户名：</span>
        <span>{{ userInfo.username }}</span>
      </div>
      <div class="user_info_item">
        <span>邮箱：</span>
        <span>{{ userInfo.email }}</span>
      </div>
      <div class="user_info_item">
        <span>性别：</span>
        <el-select v-model="userInfo.sex" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="user_info_item">
        <span>个人简介:</span>
        <el-input
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 4 }"
          placeholder="请输入内容"
          v-model="userInfo.summary"
          resize="none"
          maxlength="150"
          show-word-limit
        >
        </el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="pwdDialogVisible = true">修改密码</el-button>
        <el-button type="primary" @click="closeUserVisible">确 定</el-button>
      </span>
    </el-dialog>
    <template>
      <el-dialog
        title="修改密码"
        :visible.sync="pwdDialogVisible"
        width="20%"
        :before-close="handleClose"
      >
        <div class="pwdStyle">
          <el-input
            type="password"
            v-model="password.oldPassword"
            placeholder="请输入原密码"
          ></el-input>
          <el-input
            type="password"
            v-model="password.newPassword"
            placeholder="请输入新密码"
          ></el-input>
          <el-input
            type="password"
            v-model="password.surePassword"
            placeholder="重新输入新密码"
          ></el-input>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="updatePwdCancel">取 消</el-button>
          <el-button type="primary" @click="updatePwdComfirm">确 定</el-button>
        </span>
      </el-dialog>
    </template>
  </div>
</template>

<script>
import common from "@/api/common";
import picture from "@/api/picture/picture";
import { getUUID } from "@/utils/index";

export default {
  name: "UserInfoEdit",
  props: [],
  data() {
    return {
      fileList: [],
      userDialogVisible: false, //个人资料对话框 控制
      pwdDialogVisible: false, //修改密码对话框 控制
      options: [
        {
          value: "男",
          label: "男",
        },
        {
          value: "女",
          label: "女",
        },
      ],
      userInfo: {
        //
        id: 1231234,
        username: "asdasd",
        email: "123123123@qq.com",
        sex: "男",
        avatar: require("@/assets/images/nologin.png"),
        summary: "",
      },
      password: {
        oldPassword: "",
        newPassword: "",
        surePassword: "",
      },
      uploadData: {},
      tempFilename: "",
    };
  },
  methods: {
    init(id) {
      this.userDialogVisible = true;
      common.getUserDetailById(id).then((resp) => {
        let data = resp.data.data;
        this.userInfo.id = data.id;
        this.userInfo.username = data.username;
        this.userInfo.email = data.email;
        this.userInfo.sex = data.sex;
        this.userInfo.avatar = data.avatar;
        this.userInfo.summary = data.summary;
      });
    },
    getPolicy() {
      // 从服务端获取签名直传
      picture.getPolicy().then((resp) => {
        let data = resp.data.data;
        this.uploadData.key = data.dir;
        this.uploadData.policy = data.policy;
        this.uploadData.OSSAccessKeyId = data.accessid;
        this.uploadData.signature = data.signature;
        this.uploadData.host = data.host;
      });
    },
    beforeAvatarUpload(file) {
      // 头像上传之前的操作
      this.getPolicy();

      let fileName = getUUID() + "." + file.name.split(".")[1];
      this.uploadData.key = this.uploadData.key + fileName;
      this.tempFilename = fileName;
      console.log("beforeAvatarUpload：" + JSON.stringify(this.uploadData));
    },
    handleAvatarSuccess() {
      // 头像上传成功的操作
      this.userInfo.avatar =
        this.uploadData.host + "/" + this.uploadData.key + this.tempFilename;
    },
    closeUserVisible() {
      //TODO 个人资料修改 功能
      let data = {
        id: this.userInfo.id,
        sex: this.userInfo.sex,
        avatar: this.userInfo.avatar,
        summary: this.userInfo.summary,
      };
      common.updateUserInfoByUser(data).then((resp) => {
        if (resp.data.code === 200) {
          this.$message({
            message: resp.data.data,
            type: "success",
          });
          this.userDialogVisible = false;
          this.$emit("refreshUserInfo");
        } else {
          this.$message({
            message: resp.data.data,
            type: "error",
          });
        }
      });
    },
    updatePwdComfirm() {
      //TODO 修改密码功能
      this.pwdDialogVisible = false;
      this.$message({
        message: "修改密码成功",
        type: "success",
      });
      this.clearPwd();
    },
    updatePwdCancel() {
      //TODO 取消修改密码
      this.pwdDialogVisible = false;
      this.clearPwd();
    },
    clearPwd() {
      this.password.oldPassword = "";
      this.password.newPassword = "";
      this.password.surePassword = "";
    },
  },
};
</script>

<style scoped>
.avatar-uploader {
  display: inline-block;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}

.user_info_item span {
  display: inline-block;
  /* height: 100%; */
  margin-bottom: 15px;
}

.user_info_item span:first-child {
  width: 80px;
}

.upload-demo {
  display: inline-block;
}

.el-textarea .el-input__count {
  /* font-size: 15px; */
  bottom: -35px;
}

.pwdStyle .el-input {
  margin-bottom: 10px;
}

/* 编辑对话框 的顶部偏移 */
div /deep/ .el-dialog {
  margin-top: 4vh !important;
}
</style>