import request from '@/utils/request'

export default {
    getVerifyCode(){
        return request({
            url: "/access/code",
            method: 'get'
        })
    },
    login(formData){
        return request({
            url: "/access/login",
            method: "post",
            data: formData
        })
    },
    getUserInfo(){
        return request({
            url: "/access/getUserInfo",
            method: 'get'
        })
    },
    logout(){
        return request({
            url: "/access/logout",
            method: "post",
        })
    },
    register(data){
        return request({
            url: "/access/register",
            method: "post",
            data
        })
    },
    sendCode(email){
        return request({
            url: "/mail/sendCode",
            method: "post",
            data: {email}
        })
    },
    getCategoryTree(){
        return request({
            url: "/kkc/category/treeList",
            method: "get",
        })
    },
    writeArticle(data){
        return request({
            url: "/write/article/write",
            method: "post",
            data
        })
    },
    writeQuestion(data){
        return request({
            url: "/write/question/write",
            method: "post",
            data
        })
    },
    getIndexArticle(){
        return request({
            url: "/kkc/index/article/list",
            method: "get",
        })
    },
    getIndexQuestion(){
        return request({
            url: "/kkc/index/question/list",
            method: "get",
        })
    },
    getArticleListByCondition(data){
        return request({
            url: "/kkc/nav/article",
            method: "get",
            params: data
        })
    },
    getQuestionListByCondition(data){
        return request({
            url: "/kkc/nav/question",
            method: "get",
            params: data
        })
    },
    getArticleShowById(id){
        return request({
            url: "/kkc/article/detail",
            method: "get",
            params: {
                id
            }
        })
    },
    getUserAchievementById(id){
        return request({
            url: "/kkc/user/achievement",
            method: "get",
            params: {
                id
            }
        })
    },
    getArticleComment(id){
        return request({
            url: "/kkc/article/comment",
            method: "get",
            params: {
                id
            }
        })
    },
    postArticleComment(data){
        return request({
            url: "/post/article/post",
            method: "post",
            data
        })
    },
    checkUserFollow(data){
        return request({
            url: "/follow/check",
            method: "post",
            data
        })
    },
    userFollow(data){
        return request({
            url: "/follow/confirm",
            method: "post",
            data
        })
    },
    userCancelFollow(data){
        return request({
            url: "/follow/cancel",
            method: "post",
            data
        })
    },
    getQuestionShowById(id){
        return request({
            url: "/kkc/question/detail",
            method: "get",
            params: {
                id
            }
        })
    },
    getQuestionComment(id){
        return request({
            url: "/kkc/question/comment",
            method: "get",
            params: {
                id
            }
        })
    },
    postQuestionComment(data){
        return request({
            url: "/post/question/post",
            method: "post",
            data
        })
    },
    getUserSimpleInfoById(id){
        return request({
            url: "/user/show/getUser",
            method: "get",
            params: {
                id
            }
        })
    },
    getUserFollowList(id){
        return request({
            url: "/user/follow/list",
            method: "get",
            params: {
                id
            }
        })
    },
    getUserFollowedList(id){
        return request({
            url: "/user/followed/list",
            method: "get",
            params: {
                id
            }
        })
    },
    getUserDetailById(id){
        return request({
            url: "/user/show/getUserDetail",
            method: "get",
            params: {
                id
            }
        })
    },
    updateUserInfoByUser(data){
        return request({
            url: "/user/show/update",
            method: "post",
            data
        })
    },
    getUserPublishArticleList(id,currentPage){
        return request({
            url: "/kkc/article/user/publish",
            method: "get",
            params: {
                id,currentPage
            }
        })
    },
    getUserPublishQuestionList(id,currentPage){
        return request({
            url: "/kkc/question/user/publish",
            method: "get",
            params: {
                id,currentPage
            }
        })
    },
}