<template>
  <div>
    <Header @refreshSearchList="refreshSearchList"></Header>
    <div class="main">
      <div class="search_key_show">
        搜索的关键字：<span style="color: rgb(62, 175, 124)">{{
          $route.params.key
        }}</span>
      </div>
      <div class="article_main" v-for="data in datas" :key="data.id">
        <div
          v-if="data.mark === 'article' && data.firstImg != null"
          class="article_main_body"
        >
          <p class="article-title">
            <router-link target="_blank" :to="'/article/' + data.id">{{
              data.title
            }}</router-link>
          </p>
          <p class="article-tag">
            <el-tag
              type="info"
              v-for="tag in data.tagList"
              :key="tag.id"
              size="mini"
              ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                tag.label
              }}</router-link></el-tag
            >
          </p>
          <p class="article-describe">
            <router-link target="_blank" :to="'/article/' + data.id">{{
              data.content
            }}</router-link>
          </p>
          <p class="article-other">
            <span
              ><a :href="'/user/' + data.authorId"
                >{{ data.authorUsername }}&nbsp;&nbsp;</a
              ></span
            >
            <span>{{ data.publishTime.split("T")[0] }}</span>
            <span
              ><i class="el-icon-view"></i>&nbsp;&nbsp;{{ data.views }}</span
            >
          </p>
        </div>
        <div
          v-if="data.mark === 'article' && data.firstImg != null"
          class="article_main_img"
        >
          <router-link target="_blank" :to="'/article/' + data.id"
            ><img :src="data.firstImg"
          /></router-link>
        </div>
        <div
          v-if="data.mark === 'article' && data.firstImg == null"
          class="article_main_body_noImg"
        >
          <p class="article-title">
            <router-link target="_blank" :to="'/article/' + data.id">{{
              data.title
            }}</router-link>
          </p>
          <p class="article-tag">
            <el-tag
              type="info"
              v-for="tag in data.tagList"
              :key="tag.id"
              size="mini"
              ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                tag.label
              }}</router-link></el-tag
            >
          </p>
          <p class="article-describe">
            <router-link target="_blank" :to="'/article/' + data.id">{{
              data.content
            }}</router-link>
          </p>
          <p class="article-other">
            <span
              ><a :href="'/user/' + data.authorId"
                >{{ data.authorUsername }}&nbsp;&nbsp;</a
              ></span
            >
            <span>{{ data.publishTime.split("T")[0] }}</span>
            <span
              ><i class="el-icon-view"></i>&nbsp;&nbsp;{{ data.views }}</span
            >
          </p>
        </div>
        <div class="question_main_body" v-if="data.mark === 'question'">
          <p class="question_main_title">
            <router-link target="_blank" :to="'/question/' + data.id">{{
              data.title
            }}</router-link>
          </p>
          <p class="question_main_tags">
            <el-tag
              type="info"
              v-for="tag in data.tagList"
              :key="tag.id"
              size="mini"
              ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                tag.label
              }}</router-link></el-tag
            >
          </p>
          <div class="question_main_other">
            <span>回答{{ data.commentCount }}&nbsp;</span>
            <span>·&nbsp;</span>
            <span>观看{{ data.views }}</span>
            <div class="other_right">
              <span
                ><a
                  style="color: rgb(132, 132, 141)"
                  :href="'/user/' + data.authorId"
                  >{{ data.authorUsername }}</a
                ></span
              ><span>·&nbsp;</span>
              <span>{{ data.publishTime.split("T")[0] }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="loading">{{ noMore ? "没有更多了" : "加载中..." }}</div>
    </div>
  </div>
</template>

<script>
import search from "@/api/search";

export default {
  name: "KeySearch",
  data() {
    return {
      datas: [],
      count: 5, //默认加载数量
      isAchiveBottom: false, //滚动条是否到底部标
      noMore: false,
      searchParams: {
        key: "",
        currentPage: 1,
      },
      userImg: require("@/assets/images/nologin.png"), //如果用户没有头像，则使用这个头像
    };
  },
  created() {
    //使用window.onscroll = function(){}this指向出现问题
    //故应该使用箭头函数，因为箭头函数无this，会从上一级找，故会找到vue实例的this
    window.onscroll = () => {
      //变量scrollTop是滚动条滚动时，距离顶部的距离
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //变量windowHeight是可视区的高度
      var windowHeight =
        document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      var scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;

      //滚动条到底部的条件(距底部20px时触发加载)
      if (
        scrollTop + windowHeight >= scrollHeight - 20 &&
        !this.isAchiveBottom &&
        !this.noMore
      ) {
        // console.log(
        //   "距顶部" +
        //     scrollTop +
        //     "可视区高度" +
        //     windowHeight +
        //     "滚动条总高度" +
        //     scrollHeight
        // );
        this.isAchiveBottom = true;
        //延时触发数据加载
        setTimeout(() => {
          //后端需要进行分页，然后前端从后端拿来实现滚动加载
          //这里利用数组push来模拟从后端拿到的数据
          this.searchParams.currentPage += 1;
          this.getSearchData();
          this.isAchiveBottom = false;
        }, 500);
      }
    };
  },
  beforeMount() {},
  mounted() {
    this.getSearchData();
  },
  methods: {
    articleClick(data) {
      //TODO 具体文章点击功能
      this.$router.push({
        path: `/article/${data}`,
      });
    },
    getSearchData() {
      search
        .searchKey(this.$route.params.key, this.searchParams.currentPage)
        .then((resp) => {
          if (resp.data.data === undefined) {
            this.noMore = true;
          } else {
            if (resp.data.data.article !== undefined) {
              resp.data.data.article.forEach((e) => {
                e["mark"] = "article";
              });
              this.datas = this.datas.concat(resp.data.data.article);
            }
            if (resp.data.data.question !== undefined) {
              resp.data.data.question.forEach((e) => {
                e["mark"] = "question";
              });
              this.datas = this.datas.concat(resp.data.data.question);
            }
          }
        });
    },
    refreshSearchList() {
      this.datas = [];
      this.searchParams.currentPage = 1;
      this.getSearchData();
    },
  },
};
</script>

<style scoped>
/* 整个首页的主体 */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
  /* background-color: rgb(203, 203, 128); */
  /* z-index: 1; */
}

.search_key_show {
  width: 100%;
  height: 60px;
  /* background: grey; */
  text-align: center;
  line-height: 60px;
  font-size: 19px;
  font-weight: 600;
  margin-bottom: 10px;
}

/* 文章栏主体 */
.article_main {
  display: flex;
  width: 100%;
  background-color: white;
  border-bottom: 1px solid rgb(247, 247, 252);
}

.article_main:hover {
  background-color: rgb(238, 253, 238);
}

/* 文章栏 第一种：有图片 */
.article_main_body {
  display: inline-block;
  width: 80%;
  height: 170px;
  /* background-color: rgb(93, 177, 93); */
}

/* 文章栏 第二种：没有图片 */
.article_main_body_noImg {
  display: inline-block;
  width: 100%;
  height: 170px;
}

.article_main_img {
  display: inline-block;
  text-align: center;
  width: 20%;
  height: 100%;
  /* background-color: rgb(185, 185, 117); */
}

.article_main_img img {
  width: 80%;
  height: 80%;
  margin-top: 16px;
}

/* 标题 */
.article-title {
  width: 95%;
  margin-top: 13px;
  margin-left: 13px;
  font-size: 21px;
  text-overflow: ellipsis; /* 文字超出部分变成省略号 */
  overflow: hidden; /* 文字超出隐藏*/
  white-space: nowrap; /* 不会换行，继续在同一行文字输出*/
  font-weight: bolder; /* 加粗*/
}

a {
  text-decoration: none;
  color: black;
}

.article-describe a {
  color: gray;
}

.article-title a:hover,
.answer-title a:hover {
  color: rgb(62, 175, 124);
}

/* 标签 */
.article-tag {
  margin-left: 28px;
  margin-top: 5px;
  margin-bottom: 15px;
}

.article-tag .el-tag {
  margin-right: 5px;
}

/* 描述 */
.article-describe {
  width: 95%;
  height: 60px;
  margin-left: 13px;
  font-size: 15px;
  color: gray;

  /* 多行文字过长自动省略 */
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: -8px;
}

/* 其他 */
.article-other {
  margin-top: 8px;
  margin-left: 13px;
  color: black;
}

.article-other span {
  display: inline-block;
  /* width: 100px; */
  margin-right: 20px;
  font-size: 14px;
  /* border-right: 1px solid grey; */
}

.article-other span:last-child {
  border-right: 0px;
}

/* 问答栏主体 */
.question_main {
  display: flex;
  width: 100%;

  background-color: white;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 问答栏 文字显示部分 */
.question_main_body {
  display: inline-block;
  width: 100%;
  height: 110px;
  /* background-color: rgb(93, 177, 93); */
}

/* 问答栏 标题 */
.question_main_title {
  width: 79%;
  margin-top: 13px;
  margin-left: 13px;
  font-size: 21px;
  /* background-color: grey; */
  text-overflow: ellipsis; /* 文字超出部分变成省略号 */
  overflow: hidden; /* 文字超出隐藏*/
  white-space: nowrap; /* 不会换行，继续在同一行文字输出*/
  font-weight: bolder; /* 加粗*/
}

.question_main_title:hover {
  color: rgb(62, 175, 124);
}

.question_main_tags,
.question_main_other {
  margin-top: 7px;
  margin-left: 13px;
}

.question_main_tags span {
  margin-right: 9px;
}

.question_main_other span {
  display: inline-block;
  margin-right: 5px;
  font-size: 14px;
  color: rgb(132, 132, 141);
}

.other_right {
  float: right;
  display: block;
  width: 25%;
  text-align: right;
  margin-right: 16px;
  /* background-color: grey; */
}

/* 自动滚动 底部栏主题 */
.loading {
  width: 100%;
  height: 60px;
  background-color: white;
  text-align: center;
  line-height: 60px;
  color: rgb(132, 132, 141);
}
</style>