import VueRouter from 'vue-router'
import Index from "@/components/Index.vue"
import Login from "@/components/Login.vue"
import Register from "@/components/Register.vue"
import ArticleList from "@/components/article/ArticleList.vue"
import ArticleDetail from "@/components/article/ArticleDetail.vue"
import ArticleWrite from "@/components/article/ArticleWrite.vue"
import QuestionList from "@/components/question/QuestionList.vue"
import QuestionDetail from "@/components/question/QuestionDetail.vue"
import QuestionWrite from "@/components/question/QuestionWrite.vue"
import KeySearch from '@/components/search/KeySearch.vue'
import TagSearch from '@/components/search/TagSearch.vue'
import UserInfo from '@/components/user/UserInfo.vue'

import cookie from 'js-cookie'

let router = new VueRouter({
    mode: 'history',
    routes: [
        { name: "Index", path: '/', component: Index },
        { name: "login", path: '/login', component: Login },
        { name: "register", path: '/register', component: Register },
        { name: "articleList", path: '/article', component: ArticleList },
        { name: "articleWrite", path: "/article/write", component: ArticleWrite },
        { name: "articleDetail", path: "/article/:id", component: ArticleDetail },
        { name: "questionList", path: '/question', component: QuestionList },
        { name: "questionWrite", path: '/question/write', component: QuestionWrite },
        { name: "questionDetail", path: '/question/:id', component: QuestionDetail },
        { name: "keySearch", path: '/search/:key', component: KeySearch },
        { name: "tagSearch", path: '/tag/:id', component: TagSearch },
        { name: "userInfo", path: '/user/:id', component: UserInfo },
    ],
})

router.beforeEach((to, from, next) => {
    if (to.path === "/article/write" || to.path === "/question/write") {
        let token = cookie.get("token");
        if (token !== undefined) {
            next();
        } else {
            next({path: "/login",params: {error: 100}});
        }
    } else {
        next();
    }
})
export default router;