<template>
  <div>
    <div class="write-header">
      <el-row>
        <el-col :span="3">
          <div class="write-header-return">
            <span @click="$router.go(-1)"
              ><i class="el-icon-arrow-left"></i>返回上一页</span
            >
          </div>
        </el-col>
        <el-col :span="17">
          <el-input
            type="text"
            placeholder="请输入内容文章标题"
            v-model="article.title"
            maxlength="60"
            show-word-limit
          >
          </el-input>
        </el-col>
        <el-col :span="4">
          <div class="write-header-publish">
            <span @click="saveEditor">保存文章</span>
            <span @click="publicClick">发布文章</span>
          </div>
        </el-col>
      </el-row>
    </div>
    <div :style="{ height: editorHeight + 'px' }">
      <mavon-editor
        ref="md"
        @save="saveEditor"
        @imgAdd="imgAddClick"
        @imgDel="imgDelClick"
        style="height: 100%"
        v-model="article.content"
      />
    </div>
    <template>
      <el-dialog
        title="检测好并确认发布吗"
        :visible.sync="dialogVisible"
        :close-on-click-modal="false"
        width="30%"
        center
      >
        <div>
          <span>该文章所属的分类：</span>
          <el-select
            v-model="article.categoryId"
            placeholder="请选择"
            @change="categoryClick"
          >
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </div>
        <div style="margin-top: 15px; margin-bottom: 15px">
          <span>该文章所属的标签：</span>
          <el-select
            v-model="article.tagIds"
            multiple
            placeholder="请选择"
            :disabled="tagFlag"
          >
            <el-option
              v-for="item in tagOptions"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </div>
        <div style="text-align: center">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="publishRelease">确 定</el-button>
        </div>
      </el-dialog>
    </template>
  </div>
</template>

<script>
import picture from "@/api/picture/picture";
// import axios from 'axios';
import base from "@/utils/base";
import common from "@/api/common";

export default {
  name: "ArticleWrite",
  data() {
    return {
      editorHeight: window.innerHeight - 60 - 1,
      dialogVisible: false,
      options: [],
      tagOptions: [],
      article: {
        authorId: "",
        categoryId: "",
        tagIds: [],
        content: "",
        title: "",
      },
      tagFlag: true,
      img_file: {},
      relationData: {},
    };
  },
  mounted() {
    this.getEditor();
    this.getUserInfo();
    this.$nextTick(() => {
      window.addEventListener("resize", () => {
        //监听浏览器窗口大小改变
        //对文本编辑器的高度 进行重新调整
        this.editorHeight = window.innerHeight - 60 - 1;
      });
    });
  },
  methods: {
    getUserInfo() {
      common.getUserInfo().then((resp) => {
        let data = resp.data.data;
        this.article.authorId = data.id;
      });
    },
    publishRelease() {
      //TODO 发布文章功能
      if (this.article.categoryId.length === 0) {
        alert("必须选择一个分类");
      } else if (this.article.tagIds.length === 0) {
        alert("请选择至少一个标签");
      } else {
        picture.getPolicy().then((resp) => {
          let data = resp.data.data;

          for (let i in this.img_file) {
            var formdata = new FormData();
            let fileName =
              data.dir +
              this.title.slice(0, 15) +
              "/" +
              this.img_file[i].name.replaceAll(" ", "");
            formdata.append("key", fileName);
            formdata.append("policy", data.policy);
            formdata.append("OSSAccessKeyId", data.accessid);
            formdata.append("signature", data.signature);
            formdata.append("host", data.host);
            formdata.append("file", this.img_file[i]);
            base({
              method: "post",
              data: formdata,
            });
            this.$refs.md.$img2Url(i, data.host + "/" + fileName);
          }
        });
        //TODO 发布文章功能
        common.writeArticle(this.article).then((resp) => {
          if (resp.data.code == 200) {
            this.clearEditor();
            this.dialogVisible = false;
            this.$router.push({ path: "/" });
            this.$message({
              message: resp.data.data,
              type: "success",
            });
          }
        });
      }
    },
    publicClick() {
      this.dialogVisible = true;
      common.getCategoryTree().then((resp) => {
        let relationData = resp.data.data;
        for (let i = 0; i < relationData.length; i++) {
          this.options.push({
            id: relationData[i].id,
            label: relationData[i].label,
          });
          this.relationData[relationData[i].id] = relationData[i].children;
        }
      });
    },
    saveEditor() {
      if (this.title.length === 0) {
        this.$message({
          message: "请先填写一下标题之后在保存",
          type: "warning",
        });
        return;
      }

      //TODO 保存当前编写的文章，并继续编辑
      localStorage.setItem("write-article-content", this.content);
      localStorage.setItem("write-acticle-title", this.title);

      this.$notify({
        title: "保存成功",
        message: "",
        type: "success",
        duration: 2000, //跳出的提示框只显示两秒钟
        offset: 50, //设置跳出的位置 偏移量
      });
      this.imgwholeUpload();
    },
    getEditor() {
      //获取在 localStroage保存的 文本及标题
      let content = localStorage.getItem("write-article-content");
      let title = localStorage.getItem("write-acticle-title");
      if (content !== null) {
        this.content = content;
      }
      if (title !== null) {
        this.title = title;
      }
    },
    clearEditor() {
      //清空 localStroage保存的 文本及标题
      localStorage.removeItem("write-article-content");
      localStorage.removeItem("write-acticle-title");
    },
    categoryClick(id) {
      this.article.tagIds = [];
      //点击分类时触发的事件
      this.tagFlag = false;

      this.tagOptions = this.relationData[id];
    },
    imgAddClick(pos, file) {
      //在这里限定后缀只能是jpg、jpeg、png

      // 缓存图片信息
      this.img_file[pos] = file;
    },
    imgDelClick(pos) {
      delete this.img_file[pos];
    },
    imgwholeUpload() {
      picture.getPolicy().then((resp) => {
        let data = resp.data.data;

        for (let i in this.img_file) {
          var formdata = new FormData();
          let fileName =
            data.dir +
            this.title.slice(0, 15) +
            "/" +
            this.img_file[i].name.replaceAll(" ", "");
          formdata.append("key", fileName);
          formdata.append("policy", data.policy);
          formdata.append("OSSAccessKeyId", data.accessid);
          formdata.append("signature", data.signature);
          formdata.append("host", data.host);
          formdata.append("file", this.img_file[i]);
          base({
            method: "post",
            data: formdata,
          });
          this.$refs.md.$img2Url(i, data.host + "/" + fileName);
        }
      });
    },
  },
};
</script>

<style scoped>
/* 写文章 头部主题 */
.write-header {
  width: 100%;
  height: 60px;
  /* background-color: grey; */
}

/* 返回上一页 样式 */
.write-header-return {
  text-align: center;
  /* line-height: 60px; */
}

.write-header-return span {
  display: inline-block;
  width: 120px;
  height: 40px;
  /* background-color: white; */
  margin-top: 8px;
  line-height: 40px;
  /* border: 1px solid rgb(62, 175, 124); */
  color: rgb(129, 144, 150);
  font-size: 20px;
}

.write-header-return span:hover,
.write-header-publish span:hover {
  cursor: pointer;
}

/* 发布文章 按钮 */
.write-header-publish {
  margin-top: 8px;
  width: 100%;
}

.write-header-publish span {
  margin-left: 12px;
  display: inline-block;
  width: 110px;
  height: 40px;
  font-size: 18px;
  border: 1px solid rgb(62, 175, 124);
  color: rgb(62, 175, 124);
  border-radius: 30px;
  text-align: center;
  line-height: 40px;
}

.write-header-publish span:first-child {
  background-color: rgb(62, 175, 124);
  color: white;
}

.write-header /deep/ .el-input__inner {
  margin-top: 8px;
  border-radius: 30px;
}

div /deep/ .el-input__count-inner {
  margin-top: 10px;
  font-size: 16px;
}
</style>