  text-decoration: none;
  color: black;
}
/* 整个首页的主体 */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
}

/* 文章页 大标签栏 */
.article_nav {
  width: 100%;
  background-color: white;
  margin-bottom: 10px;
}

.article_nav span {
  display: inline-block;
  height: 100%;
  width: 100px;
}

/* 对标签栏个体高度进行修改 */
.el-menu-item {
  height: 50px;
  line-height: 50px;
}

/* 文章页 点击大标签栏后的 小标签 */
.article_sub_nav {
  width: 100%;
  margin-bottom: 10px;
}

.sub_tag {
  margin-top: 10px;
  margin-right: 10px;
  padding-left: 9px;
  padding-right: 9px;
  padding-top: 5px;
  /* padding-bottom: 2px; */
  display: inline-block;
  height: 24px;
  /* background-color: rgb(62, 175, 124); */
  background-color: white;
  font-size: 13px;
  border-radius: 8px;
}

/* 问题页 列表主题(包括问题主体和侧边主体) */
.article_body {
  width: 100%;
}

/* 公告栏主体 */
.article_bulletin_area {
  width: 100%;
  height: 300px;
  background-color: white;
}

/* 侧边主题： 公告/随便看看 标题 */
.article_bulletin_tip,
.article_random_tip {
  height: 55px;
  width: 100%;
  /* background-color: green; */
  line-height: 55px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 公告栏/随便看看 标题 */
.article_bulletin_tip span,
.article_random_tip span {
  margin-left: 15px;
  font-size: 18px;
  font-weight: bolder;
}

.article_bulletin_list {
  width: 100%;
  height: 100%;
}

.article_bulletin_node {
  height: 47px;
  width: 100%;
  line-height: 47px;
  font-size: 14px;
}

.article_bulletin_node .title {
  display: inline-block;
  width: 80%;
  margin-left: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 随便看看 主体 */
.article_random_area {
  position: sticky;
  top: 70px;
  /* bottom: 0; */
  margin-top: 20px;
  width: 100%;
  height: 460px;
  background-color: white;
}

/* 随便看看 文章栏 */
.article_random_node {
  height: 80px;
  width: 100%;
  /* background-color: yellow; */
}

/* 标题 */
.article_random_title {
  padding-top: 18px;
  margin-left: 13px;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 其他部分 */
.article_random_other {
  margin-top: 10px;
}

/* 其他内容 */
.article_random_other span {
  margin-left: 13px;
  display: inline-block;
  font-size: 13px;
}

.article_random_other span img {
  width: 28px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
}

.article_random_other span:last-child {
  width: 30%;
  float: right;
  /* background-color: yellow; */
  margin-top: 5px;
}

.article_condition {
  width: 98%;
  background-color: white;
  height: 40px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 对条件栏个体高度进行修改 */
.article_condition .el-menu-item {
  height: 40px;
  line-height: 40px;
}

/* 文章栏主体 */
.article_main {
  display: flex;
  width: 98%;
  height: 110px;
  background-color: white;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 自动滚动 底部栏主题 */
.loading {
  width: 98%;
  height: 60px;
  background-color: white;
  text-align: center;
  line-height: 60px;
  color: rgb(132, 132, 141);
}

/* 文章栏 文字显示部分 */
.article_main_body {
  display: inline-block;
  width: 100%;
  height: 100%;
  /* background-color: rgb(93, 177, 93); */
}

.article_main:hover {
  background-color: rgb(238, 253, 238);
  cursor: pointer; /* 当移动到指定位置时 鼠标样式发生变化 */
}

/* 问答栏 标题 */
.article_main_title {
  width: 90%;
  margin-top: 13px;
  margin-left: 13px;
  font-size: 21px;
  /* background-color: grey; */
  text-overflow: ellipsis; /* 文字超出部分变成省略号 */
  overflow: hidden; /* 文字超出隐藏*/
  white-space: nowrap; /* 不会换行，继续在同一行文字输出*/
  font-weight: bolder; /* 加粗*/
}

.article_main_title a:hover,
.article_random_title a:hover {
  color: rgb(62, 175, 124);
}

.article_main_tags,
.article_main_other {
  margin-top: 7px;
  margin-left: 13px;
}

.article_main_tags span {
  margin-right: 9px;
}

.article_main_other span {
  display: inline-block;
  margin-right: 5px;
  font-size: 14px;
  color: rgb(132, 132, 141);
}

.other_right {
  float: right;
  display: block;
  width: 30%;
  text-align: right;
  margin-right: 16px;
  /* background-color: grey; */
}
</style>