<template>
  <div>
    <Header></Header>
    <div class="main">
      <div class="user_info">
        <el-row>
          <el-col :span="4">
            <div class="user_img">
              <img :src="userInfo.avatar == null ? userImg : userInfo.avatar" />
            </div>
            <p
              v-if="loginId != $route.params.id"
              style="margin-top: 5px"
              class="author_follow"
            >
              <span @click="authorFollow" v-if="!isFollow"
                ><i class="el-icon-plus"></i>关注</span
              >
              <span @click="undoAuthorFollow" v-else>已关注</span>
            </p>
          </el-col>
          <el-col :span="15">
            <div class="user_title">
              <span>{{ userInfo.username }}</span>
            </div>
            <div class="user_brief">
              <span><b>关注：</b>{{ userInfo.followedCount }}</span
              ><span><b>粉丝：</b>{{ userInfo.followerCount }}</span
              ><span
                ><b>加入时间：</b>{{ userInfo.createTime.split("T")[0] }}</span
              ><br />
              <div class="user_brief_content">
                <span><b>个人简介：</b></span>
                <span>
                  {{ userInfo.summary }}
                </span>
              </div>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="user_achievement">
              <div class="user_achievement_title">
                <span style="display: inline-block; line-height: 50px"
                  >其他统计</span
                >
              </div>
              <div class="user_achievement_info">
                <div class="user_achievement_info_count">
                  <div class="user_achievement_info_item">
                    <div class="user_achievement_info_item_label">点赞</div>
                    <div class="user_achievement_info_item_number">
                      <span class="number_style">{{
                        userOther.thumbsUpCount
                      }}</span>
                    </div>
                  </div>
                  <div class="user_achievement_info_item">
                    <div class="user_achievement_info_item_label">浏览</div>
                    <div class="user_achievement_info_item_number">
                      <span class="number_style">{{
                        userOther.viewsCount
                      }}</span>
                    </div>
                  </div>
                  <div class="user_achievement_info_item">
                    <div class="user_achievement_info_item_label">原创</div>
                    <div class="user_achievement_info_item_number">
                      <span class="number_style">{{
                        userOther.originalCount
                      }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="original_body">
        <el-col :span="18">
          <div class="article_condition">
            <el-menu
              v-if="this.$route.params.id == loginId"
              :default-active="conditionIndex"
              class="el-menu-demo"
              mode="horizontal"
              active-text-color="#3EAF7C"
              @select="handleSelect2"
            >
              <el-menu-item
                v-for="data in conditionList"
                :key="data.index"
                :index="data.index"
                >{{ data.category }}</el-menu-item
              >
            </el-menu>
            <el-menu
              v-else
              :default-active="conditionIndex"
              class="el-menu-demo"
              mode="horizontal"
              active-text-color="#3EAF7C"
              @select="handleSelect2"
            >
              <el-menu-item
                v-for="data in otherConditionList"
                :key="data.index"
                :index="data.index"
                >{{ data.category }}</el-menu-item
              >
            </el-menu>
          </div>
          <div
            class="article_main"
            v-show="conditionIndex === '10000'"
            v-for="data in articleList"
            :key="data.id"
          >
            <div v-if="data.firstImg != null" class="article_main_body">
              <p class="article-title">
                <router-link target="_blank" :to="'/article/' + data.id">{{
                  data.title
                }}</router-link>
              </p>
              <p class="article-tag">
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.official == 1"
                  >官方</el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.top == 1"
                  >置顶</el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.essence == 1"
                  >精华</el-tag
                >
                <el-tag
                  v-for="tag in data.tagList"
                  :key="tag.id"
                  type="info"
                  size="mini"
                  ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                    tag.label
                  }}</router-link></el-tag
                >
              </p>
              <p class="article-describe">
                <router-link target="_blank" :to="'/article/' + data.id">
                  {{ data.content }}</router-link
                >
              </p>
              <p class="article-other">
                <span
                  ><router-link target="_blank" :to="'/user/' + data.authorId"
                    >{{ data.authorUsername }}&nbsp;&nbsp;</router-link
                  ></span
                >
                <span>{{ data.publishTime.split("T")[0] }}</span>
                <span
                  ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                    data.views
                  }}</span
                >
              </p>
            </div>
            <div v-else class="article_main_body_noImg">
              <p class="article-title">
                <router-link target="_blank" :to="'/article/' + data.id">{{
                  data.title
                }}</router-link>
              </p>
              <p class="article-tag">
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.official == 1"
                  >官方</el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.top == 1"
                  >置顶</el-tag
                >
                <el-tag
                  size="small"
                  style="color: rgb(62, 175, 124)"
                  color="white"
                  v-if="data.essence == 1"
                  >精华</el-tag
                >
                <el-tag
                  v-for="tag in data.tagList"
                  :key="tag.id"
                  type="info"
                  size="mini"
                  ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                    tag.label
                  }}</router-link></el-tag
                >
              </p>
              <p class="article-describe">
                <router-link target="_blank" :to="'/article/' + data.id">
                  {{ data.content }}</router-link
                >
              </p>
              <p class="article-other">
                <span
                  ><router-link
                    target="_blank"
                    :to="'/user/' + data.authorId"
                    >{{ data.authorUsername }}</router-link
                  >&nbsp;&nbsp;</span
                >
                <span>{{ data.publishTime.split("T")[0] }}</span>
                <span
                  ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                    data.views
                  }}</span
                >
              </p>
            </div>
            <div v-if="data.firstImg != null" class="article_main_img">
              <router-link target="_blank" :to="'/article/' + data.id"
                ><img :src="data.firstImg"
              /></router-link>
            </div>
          </div>
          <div
            class="question_main"
            v-show="conditionIndex === '10001'"
            v-for="data in questionList"
            :key="data.id"
          >
            <div class="question_main_body">
              <p class="question_main_title">
                <router-link target="_blank" :to="'/question/' + data.id">{{
                  data.title
                }}</router-link>
              </p>
              <p class="question_main_tags">
                <el-tag
                  v-for="tag in data.tagList"
                  :key="tag.id"
                  type="info"
                  size="mini"
                  ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                    tag.label
                  }}</router-link></el-tag
                >
              </p>
              <div class="question_main_other">
                <span>回答{{ data.commentCount }}&nbsp;</span>
                <span>·&nbsp;</span>
                <span>观看{{ data.views }}</span>
                <div class="other_right">
                  <span
                    ><router-link target="_blank" :to="'/user/' + data.id">{{
                      data.authorUsername
                    }}</router-link></span
                  ><span>·&nbsp;</span>
                  <span>{{ data.publishTime.split("T")[0] }}</span>
                </div>
              </div>
            </div>
          </div>
          <div v-show="conditionIndex === '10002'" class="message_main">
            <div class="message_main_thumbsUp">
              <div class="thumbsUp_avatar">
                <img src="@/assets/images/nologin.png" />
              </div>
              <div class="thumbsUp_show">
                <span
                  ><router-link to="/user/1d3a"><b>作者</b></router-link> ·
                  赞了这篇文章</span
                >
              </div>
              <div class="thumbsUp_link">
                <span
                  >标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题</span
                >
              </div>
            </div>
            <div class="message_main_comment">
              <div class="thumbsUp_avatar">
                <img src="@/assets/images/nologin.png" />
              </div>
              <div class="thumbsUp_show">
                <span
                  ><router-link to="/user/1d3a"><b>作者</b></router-link> ·
                  进行了评论</span
                >
              </div>
              <div class="thumbsUp_link">
                <span
                  >标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题标题</span
                >
              </div>
            </div>
          </div>
          <div class="loading">{{ noMore ? "没有更多了" : "加载中..." }}</div>
        </el-col>
        <el-col :span="6">
          <div class="article_aside">
            <div
              v-if="this.$route.params.id == loginId"
              class="user_aside_area"
            >
              <div class="article_random_tip">
                <span>个人资料</span>
                <span
                  @click="sendUserVisible()"
                  style="float: right; margin-right: 15px; font-size: 14px"
                  >点击查看</span
                >
              </div>
            </div>
            <div class="article_bulletin_area">
              <div class="article_bulletin_tip">
                <span>关注的人</span>
                <span
                  @click="followClick"
                  style="float: right; margin-right: 15px; font-size: 14px"
                  >点击查看</span
                >
              </div>
            </div>
            <div class="article_random_area">
              <div class="article_random_tip">
                <span>粉丝</span>
                <span
                  @click="followedClick"
                  style="float: right; margin-right: 15px; font-size: 14px"
                  >点击查看</span
                >
              </div>
            </div>
          </div>
        </el-col>
      </div>
    </div>
    <el-drawer title="关注列表" :visible.sync="followDrawer" direction="rtl">
      <div class="user_follow">
        <div class="user_follow_item" v-for="data in followData" :key="data.id">
          <img :src="data.avatar == null ? userImg : data.avatar" />
          <span
            ><router-link target="_blank" :to="'/user/' + data.id">{{
              data.username
            }}</router-link></span
          >
        </div>
      </div>
    </el-drawer>
    <el-drawer title="粉丝列表" :visible.sync="followedDrawer" direction="rtl">
      <div class="user_follow">
        <div
          class="user_follow_item"
          v-for="data in followedData"
          :key="data.id"
        >
          <img :src="data.avatar == null ? userImg : data.avatar" />
          <span
            ><router-link target="_blank" :to="'/user/' + data.id">{{
              data.username
            }}</router-link></span
          >
        </div>
      </div>
    </el-drawer>
    <UserInfoEdit
      v-if="userEditDialog"
      ref="userEdit"
      @refreshUserInfo="getUserSimpleInfoById"
    ></UserInfoEdit>
  </div>
</template>

<script>
import UserInfoEdit from "./UserInfoEdit.vue";
import common from "@/api/common";
import cookie from "js-cookie";

export default {
  name: "UserInfo",
  components: { UserInfoEdit },
  data() {
    return {
      isFollow: false, //是否关注
      conditionList: [
        //条件列表，主要用于个人信息 导航点击
        { index: "10000", category: "我的文章" },
        { index: "10001", category: "我的问答" },
        { index: "10002", category: "我的消息" },
      ],
      otherConditionList: [
        //条件列表，主要用于个人信息 导航点击
        { index: "10000", category: "我的文章" },
        { index: "10001", category: "我的问答" },
      ],
      conditionIndex: "10000", //导航点击 后返回的索引号
      datas: [],
      followData: [], //关注数据列表
      followedData: [], //粉丝数据列表
      noMore: false, //是否还有更多
      isAchiveBottom: false, //滚动条是否到底部标
      articleFlag: true, //默认显示文章栏内容， false显示问答内容
      followDrawer: false, //关注列表的对话框显示
      followedDrawer: false, //粉丝列表的对话框显示
      userEditDialog: false, //个人资料对话框显示
      userInfo: {
        //用户信息的显示
        id: 100002, //用户id
        username: "kerco123", //用户名
        sex: "男", //用户性别
        followerCount: 1, //关注数
        followedCount: 1, //粉丝数
        avatar: null, //头像
        summary: null, //个人简介
        createTime: "2023-01-14T07:13:01", //加入时间
      },
      userOther: {
        //用户其他信息
        originalCount: 0, //原创数
        thumbsUpCount: 0, //点赞数
        viewsCount: 0, //浏览数
      },
      userImg: require("@/assets/images/nologin.png"), //如果用户没有头像，则使用该头像
      loginId: "", //登录用户的id，主要用于对个人资料显示的判断及编辑
      articleParams: {
        //我的文章 参数
        currentPage: 1, //当前页
        isAchiveBottom: false, //滚动条是否到底部标
        noMore: false,
      },
      questionParams: {
        //我的问答 参数
        currentPage: 1,
        isAchiveBottom: false, //滚动条是否到底部标
        noMore: false,
      },
      messageParams: {
        //我的消息 参数
        currentPage: 1,
        isAchiveBottom: false, //滚动条是否到底部标
        noMore: false,
      },
      articleList: [], //文章列表数据
      questionList: [], //问答列表数据
      follow: {
        //关注参数
        followId: null, //关注人id
        userId: null, //发起关注人id
      },
    };
  },
  created() {
    //使用window.onscroll = function(){}this指向出现问题
    //故应该使用箭头函数，因为箭头函数无this，会从上一级找，故会找到vue实例的this
    window.onscroll = () => {
      //变量scrollTop是滚动条滚动时，距离顶部的距离
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //变量windowHeight是可视区的高度
      var windowHeight =
        document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      var scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
      //滚动条到底部的条件(距底部20px时触发加载)
      if (
        scrollTop + windowHeight >= scrollHeight - 20 &&
        !this.isAchiveBottom &&
        !this.noMore
      ) {
        // console.log(
        //   "距顶部" +
        //     scrollTop +
        //     "可视区高度" +
        //     windowHeight +
        //     "滚动条总高度" +
        //     scrollHeight
        // );
        if (this.conditionIndex == "10000") {
          this.articleParams.isAchiveBottom = true;
        } else if (this.conditionIndex == "10001") {
          this.questionParams.isAchiveBottom = true;
        }
        //延时触发数据加载
        setTimeout(() => {
          //后端需要进行分页，然后前端从后端拿来实现滚动加载
          //这里利用数组push来模拟从后端拿到的数据
          if (this.conditionIndex == "10000") {
            this.articleParams.currentPage += 1;
            this.getUserPublishArticleList();
            this.articleParams.isAchiveBottom = false;
          } else if (this.conditionIndex == "10001") {
            this.questionParams.currentPage += 1;
            this.getUserPublishArticleList();
            this.questionParams.isAchiveBottom = false;
          }
        }, 500);
      }
    };
  },
  beforeMount() {
    this.getUserInfo();
  },
  mounted() {
    this.follow.followId = this.$route.params.id;
    this.getUserSimpleInfoById();
    this.getUserAchievementById(this.$route.params.id);
    this.handleSelect2("10000");
  },
  methods: {
    getUserInfo() {
      //获取登陆的用户信息，如果没有登陆则不初始化参数
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }
      common.getUserInfo().then((resp) => {
        let data = resp.data.data;
        this.loginId = data.id;
        this.follow.userId = data.id;
        this.checkUserFollow();
      });
    },
    getUserSimpleInfoById() {
      //获取链接上的用户id
      let id = this.$route.params.id;
      //需要对错误参数进行校验

      //获取用户的基本信息
      common.getUserSimpleInfoById(id).then((resp) => {
        let data = resp.data.data;
        this.userInfo.id = data.id;
        this.userInfo.username = data.username;
        this.userInfo.sex = data.sex;
        this.userInfo.followerCount = data.followerCount;
        this.userInfo.followedCount = data.followedCount;
        this.userInfo.avatar = data.avatar;
        this.userInfo.summary = data.summary;
        this.userInfo.createTime = data.createTime;
      });
    },
    getUserAchievementById(id) {
      //根据文章作者id 获取用户的成就信息
      common.getUserAchievementById(id).then((resp) => {
        let data = resp.data.data;
        this.userOther.originalCount = data.originalCount;
        this.userOther.thumbsUpCount = data.thumbsUpCount;
        this.userOther.viewsCount = data.viewsCount;
      });
    },
    checkUserFollow() {
      //检查用户是否关注
      //评论前进行验证，如果没有登陆则不检查
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }

      common.checkUserFollow(this.follow).then((resp) => {
        //返回关注结果，false：没关注 true：关注
        this.isFollow = resp.data.data;
      });
    },
    authorFollow() {
      //关注前进行验证
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "请先登陆后在操作",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }
      //TODO 作者关注点击
      common.userFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "关注成功",
            type: "success",
          });
          this.isFollow = true;
        }
      });
    },
    undoAuthorFollow() {
      //TODO 取消作者关注点击
      common.userCancelFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "取消关注成功",
            type: "success",
          });
          this.isFollow = false;
        }
      });
    },
    getUserPublishArticleList() {
      //获取我的文章信息功能

      //使用我的文章参数来进行页面控制
      if (this.articleParams.currentPage == 1) {
        //根据id获取用户发表的文章
        common
          .getUserPublishArticleList(
            this.$route.params.id,
            this.articleParams.currentPage
          )
          .then((resp) => {
            this.articleList = resp.data.data;
            if (this.articleList === undefined || resp.data.data.length < 10) {
              this.noMore = true;
              this.articleParams.noMore = true;
            }
          });
      } else {
        //根据id获取用户发表的文章
        common
          .getUserPublishArticleList(
            this.$route.params.id,
            this.articleParams.currentPage
          )
          .then((resp) => {
            if (resp.data.data === undefined) {
              this.noMore = true;
              this.articleParams.noMore = true;
              return;
            }
            this.articleList = this.articleList.concat(resp.data.data);
            //如果文章数量不是10个，说明后面已经没有了
            if (resp.data.data.length < 10) {
              this.noMore = true;
              this.articleParams.noMore = true;
            }
          });
      }
    },
    getUserPublishQuestionList() {
      //使用我的问答参数来进行页面控制
      if (this.questionParams.currentPage == 1) {
        //根据id获取用户发表的文章
        common
          .getUserPublishQuestionList(
            this.$route.params.id,
            this.questionParams.currentPage
          )
          .then((resp) => {
            this.questionList = resp.data.data;
            //如果数量小于10，表示后面已经没有数据了，不再请求
            if (this.questionList === undefined || resp.data.data.length < 10) {
              this.noMore = true;
              this.questionParams.noMore = true;
            }
          });
      } else {
        //根据id获取用户发表的文章
        common
          .getUserPublishQuestionList(
            this.$route.params.id,
            this.questionParams.currentPage
          )
          .then((resp) => {
            if (resp.data.data === undefined) {
              this.noMore = true;
              this.articleParams.noMore = true;
              return;
            }
            this.questionList = this.questionList.concat(resp.data.data);
            //如果文章数量不是10个，说明后面已经没有了
            if (resp.data.data.length < 10) {
              this.noMore = true;
              this.questionParams.noMore = true;
            }
          });
      }
    },
    handleSelect2(index) {
      //导航栏的点击，返回索引号
      this.conditionIndex = index;
      //根据索引进行判断 10000：获取我的文章数据 10001：获取我的问答数据
      if (index == "10000") {
        this.noMore = this.articleParams.noMore;
        this.isAchiveBottom = this.articleParams.isAchiveBottom;
        this.getUserPublishArticleList();
      } else if (index == "10001") {
        this.noMore = this.questionParams.noMore;
        this.isAchiveBottom = this.questionParams.isAchiveBottom;
        this.getUserPublishQuestionList();
      }
    },
    sendUserVisible() {
      //用户个人信息点击，显示用户编辑框
      this.userEditDialog = true;
      this.$nextTick(() => {
        //初始化用户个人信息
        this.$refs.userEdit.init(this.$route.params.id);
      });
    },
    refreshUserInfo() {
      //编辑完成进行页面数据刷新
      this.userEditDialog = false;
      this.getUserSimpleInfoById();
    },
    followClick() {
      //关注列表点击
      this.followDrawer = true;
      //如果有数据，则不再请求
      if (this.followData.length > 0) {
        return;
      }
      common.getUserFollowList(this.$route.params.id).then((resp) => {
        this.followData = resp.data.data;
      });
    },
    followedClick() {
      //粉丝列表点击
      this.followedDrawer = true;
      //如果有数据，则不再请求
      if (this.followedData.length > 0) {
        return;
      }
      common.getUserFollowedList(this.$route.params.id).then((resp) => {
        this.followedData = resp.data.data;
      });
    },
  },
};
</script>

