<template>
  <div>
    <Header></Header>
    <div class="main">
      <el-row>
        <el-col :span="18">
          <div class="article_body">
            <!-- 文章标题 -->
            <p>{{ question.title }}</p>
            <!-- 文章其他信息 -->
            <div class="info">
              <img :src="question.avatar == null ? userImg : question.avatar" />
              <span
                ><router-link
                  target="_blank"
                  :to="'/user/' + question.authorId"
                  >{{ question.authorUsername }}</router-link
                >&nbsp;&nbsp;</span
              >
              <span
                ><i class="el-icon-time"></i>&nbsp;{{
                  question.publishTime.split("T")[0]
                }}&nbsp;&nbsp;</span
              >
              <span
                ><i class="el-icon-view"></i>&nbsp;{{ question.views }}</span
              >
            </div>
            <!-- 解析md文本 -->
            <mavon-editor
              style="box-shadow: 0px"
              previewBackground="#fff"
              class="md"
              :value="question.content"
              :subfield="prop.subfield"
              :defaultOpen="prop.defaultOpen"
              :toolbarsFlag="prop.toolbarsFlag"
              :editable="prop.editable"
              :scrollStyle="prop.scrollStyle"
            ></mavon-editor>
            <div class="other">
              <!-- 文章标题，主要是给旁边功能的提示 -->
              <span>{{ question.title }}</span>
              <div
                style="
                  width: 23%;
                  float: right;
                  text-align: right;
                  margin-right: 16px;
                "
              >
                <span slot="reference"
                  ><a href="javascript:void(0)"
                    ><i class="fa-regular fa-thumbs-up"></i>&nbsp;{{
                      question.thumbsup
                    }}</a
                  ></span
                >
                <span
                  ><a href="javascript:void(0)"><i class="el-icon-share"></i></a
                ></span>
              </div>
            </div>
          </div>
          <div class="article_comment_body">
            <!-- 发表评论 -->
            <div class="article_comment_submit">
              <p
                style="
                  text-align: left;
                  margin-left: 20px;
                  margin-bottom: 13px;
                  font-size: 21px;
                  color: rgb(132, 132, 141);
                  font-weight: bolder;
                "
              >
                发表评论
              </p>
              <el-input
                type="textarea"
                :autosize="{ minRows: 4, maxRows: 4 }"
                placeholder="请输入内容"
                v-model.lazy="commentAreaData"
                maxlength="200"
                show-word-limit
                resize="none"
              >
              </el-input>
              <p
                style="
                  text-align: right;
                  margin-right: 20px;
                  margin-top: 13px;
                  padding-bottom: 15px;
                "
              >
                <el-button @click="commentClick" plain>提交评论</el-button>
              </p>
            </div>
            <!-- 评论区 -->
            <div class="article_comment_area">
              <p
                style="
                  margin-left: 20px;
                  margin-bottom: 13px;
                  font-size: 21px;
                  color: rgb(132, 132, 141);
                  font-weight: bolder;
                "
              >
                评论
              </p>
              <!-- 评论节点 -->
              <template v-if="commentList != null">
                <div
                  v-for="comment in commentList"
                  :key="comment.id"
                  class="article_comment_node"
                >
                  <div class="item1">
                    <img
                      :src="
                        comment.userInfo.avatar == null
                          ? userImg
                          : comment.userInfo.avatar
                      "
                    />
                    <span
                      ><router-link
                        target="_blank"
                        :to="'/user/' + comment.userInfo.id"
                        >{{ comment.userInfo.username }}</router-link
                      ></span
                    >
                    <span>{{ comment.createTime.replace("T", " ") }}</span>
                    <span @click="replyClick(comment.id, comment.userInfo.id)"
                      ><a href="javascript:void(0);"
                        ><i class="fa-regular fa-comments"></i>&nbsp;回复</a
                      ></span
                    >
                  </div>
                  <div class="item2">
                    <span>{{ comment.commentContent }}</span>
                  </div>
                  <div v-if="comment.subComment != null" class="item3">
                    <div
                      v-for="sub in comment.subComment"
                      :key="sub.key"
                      class="article_comment_sub_node"
                    >
                      <div class="sub_item1">
                        <img
                          :src="
                            sub.userInfo.avatar == null
                              ? userImg
                              : sub.userInfo.avatar
                          "
                        />
                        <span
                          ><router-link
                            target="_blank"
                            :to="'/user/' + sub.userInfo.id"
                            >{{ sub.userInfo.username }}</router-link
                          ></span
                        >
                        <span>回复</span>
                        <span style="color: black">{{
                          sub.replyUsername
                        }}</span>
                        <span>{{ sub.createTime.replace("T", " ") }}</span>
                        <span @click="replyClick(sub.parentId, sub.userInfo.id)"
                          ><a href="javascript:void(0);"
                            ><i class="fa-regular fa-comments"></i>&nbsp;回复</a
                          ></span
                        >
                      </div>
                      <div class="sub_item2">
                        <span>{{ sub.commentContent }}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
              <el-empty v-else description="还没有人评论.."></el-empty>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="article_aside">
            <div class="article_author_area">
              <div class="article_author_avator">
                <img
                  :src="question.avatar == null ? userImg : question.avatar"
                />
                <p style="margin-top: 10px; margin-bottom: 10px">
                  <router-link target="_blank" to="/user/321">{{
                    question.authorUsername
                  }}</router-link>
                </p>
                <p
                  style="margin-top: 5px; margin-bottom: 10px"
                  class="article_author_follow"
                  v-if="follow.followId != follow.userId"
                >
                  <span @click="authorFollow" v-if="!isFollow"
                    ><i class="el-icon-plus"></i>关注</span
                  >
                  <span @click="undoAuthorFollow" v-else>已关注</span>
                </p>
              </div>
              <div class="article_author_info">
                <div class="info_item">
                  <el-row>
                    <el-col :span="12"
                      ><span>原创数 {{ user.originalCount }}</span></el-col
                    >
                    <el-col :span="12"
                      ><span>粉丝数 {{ user.followedCount }}</span></el-col
                    >
                  </el-row>
                </div>
                <div class="info_item">
                  <el-row>
                    <el-col :span="12"
                      ><span>点赞数 {{ user.thumbsUpCount }}</span></el-col
                    >
                    <el-col :span="12"
                      ><span>收藏数 {{ user.collectionCount }}</span></el-col
                    >
                  </el-row>
                </div>
              </div>
            </div>
            <div class="article_random_area">
              <div class="article_random_tip">
                <span>随便看看</span>
              </div>
              <div class="article_random_node" v-for="value in 5" :key="value">
                <p class="article_random_title">
                  <router-link target="_blank" to="/question/12314"
                    >aaaaaaaaaaaaa111111111111111111111</router-link
                  >
                </p>
                <p class="article_random_other">
                  <span
                    ><img src="@/assets/images/nologin.png" />&nbsp;<router-link
                      target="_blank"
                      to="/user/12314"
                      >作者</router-link
                    ></span
                  >
                  <span> <i class="fa-regular fa-thumbs-up"></i> 1000</span>
                </p>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- 点击回复后弹出的对话框 -->
    <template>
      <el-dialog
        title="评论回复"
        :visible.sync="replyDialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <el-input
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 4 }"
          placeholder="请输入内容"
          v-model.lazy="replyAreaData"
          maxlength="200"
          show-word-limit
          resize="none"
        >
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="replyDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="replyCommentClick"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </template>
    <Footer></Footer>
  </div>
</template>

<script>
import cookie from "js-cookie";
import common from "@/api/common";

export default {
  name: "QuestionDetail",
  data() {
    return {
      prop: {
        subfield: false, // 单双栏模式
        defaultOpen: "preview", //edit： 默认展示编辑区域 ， preview： 默认展示预览区域
        editable: false,
        toolbarsFlag: false,
        scrollStyle: true,
      },
      value:
        '# 第一章 JAVA的介绍\n## 1.什么是java\nJava是你妈的\n```java\npublic class HelloWorld{\n    public static void main(String[] args){\n\tSystem.out.println("Hello World,guys");\n    }\n}\n```',
      commentAreaData: "",
      replyAreaData: "",
      replyDialogVisible: false,
      isFollow: false,
      userImg: require("@/assets/images/nologin.png"), //如果用户没有头像，则使用这个头像
      commentList: [], //评论数据
      question: {
        title: "", //问答标题
        //问答内容
        content:
          '# 第一章 JAVA的介绍\n## 1.什么是java\nJava是你妈的\n```java\npublic class HelloWorld{\n    public static void main(String[] args){\n\tSystem.out.println("Hello World,guys");\n    }\n}\n```',
        authorId: "", //作者id
        authorUsername: "", //作者名字
        authorAvatar: "", //作者头像
        thumbsup: "", //点赞数量
        views: "", //观看数
        publishTime: "", //发布时间
      },
      follow: {
        //关注参数
        followId: null, //关注人id
        userId: null, //发起关注人id
      },
      user: {
        //用户成就
        originalCount: 0, //原创数
        thumbsUpCount: 0, //点赞数
        followedCount: 0, //关注数
        collectionCount: 0, //收藏数
      },
      postComment: {
        //发表评论 参数
        id: this.$route.params.id, //评论表id
        commentId: null, //发起评论人id
        replyId: null, //回复人id
        parentId: null, //二级评论 指向一级评论的评论表id
        commentContent: "", //评论内容
      },
    };
  },
  mounted() {
    this.getQuestion();
    this.getUserInfo();
  },
  methods: {
    getUserInfo() {
      //获取登陆的用户信息，如果没有登陆则不初始化参数
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }
      common.getUserInfo().then((resp) => {
        let data = resp.data.data;
        this.postComment.commentId = data.id;
        this.follow.userId = data.id;
      });
    },
    checkUserFollow() {
      //检查用户是否关注
      //评论前进行验证，如果没有登陆则不检查
      let token = cookie.get("token");
      if (token === undefined) {
        return;
      }

      common.checkUserFollow(this.follow).then((resp) => {
        //返回关注结果，false：没关注 true：关注
        this.isFollow = resp.data.data;
      });
    },
    getQuestionComment(id) {
      //根据文章id 获取文章评论
      common.getQuestionComment(id).then((resp) => {
        this.commentList = resp.data.data;
      });
    },
    getUserAchievementById(id) {
      //根据文章作者id 获取用户的成就信息
      common.getUserAchievementById(id).then((resp) => {
        let data = resp.data.data;
        this.user.originalCount = data.originalCount;
        this.user.thumbsUpCount = data.thumbsUpCount;
        this.user.followedCount = data.followedCount;
        this.user.collectionCount = data.collectionCount;
        //在这里检查用户是否关注
        this.checkUserFollow();
      });
    },
    getQuestion() {
      //获取文章详细信息
      let id = this.$route.params.id;
      common.getQuestionShowById(id).then((resp) => {
        let data = resp.data.data;
        this.question.content = data.content;
        this.question.title = data.title;
        this.question.authorId = data.authorId;
        this.question.authorUsername = data.authorUsername;
        this.question.authorAvatar = data.authorAvatar;
        this.question.thumbsup = data.thumbsup;
        this.question.views = data.views;
        this.question.publishTime = data.publishTime;

        //更新关注参数
        this.follow.followId = data.authorId;

        //初始化数据
        this.getUserAchievementById(data.authorId);
        this.getQuestionComment(data.id);
      });
    },
    commentClick() {
      //评论前进行验证
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "请先登陆后在操作",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }

      //TODO 提交评论功能
      if (this.commentAreaData.length == 0) {
        this.$message({
          message: "评论内容不能为空",
          type: "warning",
        });
        return;
      }
      //因为这里的评论属于一级评论，则对parentId和replyId设置为null
      this.postComment.parentId = null;
      this.postComment.replyId = null;
      //评论数据赋值
      this.postComment.commentContent = this.commentAreaData;
      common.postQuestionComment(this.postComment).then((resp) => {
        if (resp.data.code == 200) {
          this.$message({
            message: "评论成功",
            type: "warning",
          });
          //刷新评论数据
          this.getQuestionComment(this.postComment.id);
        }
      });
    },
    replyClick(parentId, replyId) {
      //评论前进行验证
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "请先登陆后在操作",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }

      //TODO 回复框点击功能，将回复对话框给显示出来
      this.replyDialogVisible = true;
      //点击时将回复内容置空
      this.replyAreaData = "";
      this.postComment.parentId = parentId;
      this.postComment.replyId = replyId;
    },
    replyCommentClick() {
      // 回复评论功能
      //如果没有评论直接点击回复，则直接返回警告信息
      if (this.replyAreaData.length == 0) {
        this.$message({
          message: "回复评论内容不能为空",
          type: "warning",
        });
        return;
      }
      //对回复信息进行复制
      this.postComment.commentContent = this.replyAreaData;
      common.postQuestionComment(this.postComment).then((resp) => {
        if (resp.data.code == 200) {
          this.$message({
            message: "评论成功",
            type: "warning",
          });
          //刷新评论数据
          this.getQuestionComment(this.postComment.id);
        }
      });
      //关闭对话框
      this.replyDialogVisible = false;
    },
    authorFollow() {
      //关注前进行验证
      let token = cookie.get("token");
      if (token === undefined) {
        this.$message({
          message: "请先登陆后在操作",
          type: "warning",
        });
        this.$router.push({ path: "/login" });
        return;
      }
      //TODO 作者关注点击
      common.userFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "关注成功",
            type: "success",
          });
          this.isFollow = true;
        }
      });
    },
    undoAuthorFollow() {
      //TODO 取消作者关注点击
      common.userCancelFollow(this.follow).then((resp) => {
        if (resp.data.data) {
          this.$message({
            message: "取消关注成功",
            type: "success",
          });
          this.isFollow = false;
        }
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}
/* 整个首页的主体 */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
}

.article_aside {
  position: sticky;
}

/* 作者信息主体 */
.article_author_area {
  width: 100%;
  height: 250px;
  background-color: white;
}
/* 作者头像部分 */
.article_author_avator {
  width: 100%;
  /* height: 100px; */
  text-align: center;
}

.article_author_avator img {
  width: 70px;
  height: 70px;
  margin-top: 30px;
}

.article_author_avator .article_author_follow span {
  display: inline-block;
  width: 90px;
  height: 30px;
  background-color: rgb(62, 175, 124);
  line-height: 30px;
  color: white;
}

.article_author_avator .article_author_follow span:hover {
  cursor: pointer;
  background-color: rgb(50, 149, 104);
}

/* 作者其他信息 */
.article_author_info {
  height: 100%;
  width: 100%;
  /* background-color: grey; */
}

.info_item {
  height: 25px;
  margin-left: 30px;
  font-size: 14px;
  color: rgb(132, 132, 141);
}

/* 侧边主题： 公告/随便看看 标题 */
.article_random_tip {
  height: 55px;
  width: 100%;
  /* background-color: green; */
  line-height: 55px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 公告栏/随便看看 标题 */
.article_random_tip span {
  margin-left: 15px;
  font-size: 18px;
  font-weight: bolder;
}
/* 随便看看 主体 */
.article_random_area {
  position: sticky;
  top: 0px;
  margin-top: 20px;
  width: 100%;
  height: 460px;
  background-color: white;
}

/* 随便看看 文章栏 */
.article_random_node {
  height: 80px;
  width: 100%;
  /* background-color: yellow; */
}

/* 标题 */
.article_random_title {
  padding-top: 18px;
  margin-left: 13px;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.article_random_title a:hover {
  color: rgb(62, 175, 124);
}

/* 其他部分 */
.article_random_other {
  margin-top: 10px;
}

.article_random_title a,
.article_random_other span a,
.other span a {
  text-decoration: none;
  color: black;
}

/* 其他内容 */
.article_random_other span {
  margin-left: 13px;
  display: inline-block;
  width: 30%;
  font-size: 13px;
}

.article_random_other span img {
  width: 28px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
}

.article_random_other span:last-child {
  width: 30%;
  float: right;
  /* background-color: yellow; */
  margin-top: 5px;
}

/* 文章主体 */
.article_body {
  width: 98%;
  background-color: white;
}

.article_body p {
  padding-top: 25px;
  margin-left: 25px;
  font-size: 32px;
  font-weight: bolder;
}

/* 文章末尾栏 */
.other {
  border-top: 2px solid rgb(247, 247, 252);
  width: 98%;
  padding-top: 15px;
  padding-left: 25px;
  padding-bottom: 15px;
  color: rgb(99, 99, 104);
}

.other div span {
  margin-right: 15px;
}

/* 文章信息介绍栏 */
.info {
  margin-top: 25px;
  font-size: 14px;
  color: rgb(132, 132, 141);
}

.info img {
  margin-left: 25px;
  width: 35px;
  height: 35px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
}

.info span {
  margin-left: 15px;
  border-right: 1px solid rgb(178, 173, 173);
}

.info span a {
  text-decoration: none;
  color: black;
}

.info span:last-child {
  border-right: 0px;
}

/* 文章评论主体 */
.article_comment_body {
  margin-top: 15px;
  width: 98%;
  background-color: white;
}

/* 发表评论主体 */
.article_comment_submit {
  text-align: center;
  width: 100%;
  padding-top: 13px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 评论框 */
.article_comment_submit .el-textarea {
  width: 97%;
}

/* 查看评论主体 */
.article_comment_area {
  width: 100%;
  padding-top: 15px;
}

.article_comment_node {
  margin-left: 16px;
  padding-bottom: 10px;
}

/* 评论区 内容第一行 */
/* 图片 */
.item1 img {
  width: 40px;
  height: 40px;
  vertical-align: middle;
  border-radius: 25px;
}
/* 其他内容 */
.item1 span {
  margin-left: 15px;
  display: inline-block;
  height: 40px;
  color: rgb(132, 132, 141);
}

.item1 span:last-child,
.sub_item1 span:last-child {
  float: right;
  text-align: right;
  margin-right: 30px;
}

.item1 span:last-child {
  margin-right: 45px;
}

.item1 span a,
.item3 .sub_item1 span a {
  text-decoration: none;
  color: black;
}

/* 评论区 内容第二行 */
/* 评论 */
.item2 {
  width: 92%;
  margin-left: 55px;
  padding-bottom: 10px;
}

.item2 span {
  font-size: 14px;
}

/* 二级评论区 */
.article_comment_sub_node {
  margin-bottom: 10px;
}

.item3 {
  width: 92%;
  margin-left: 55px;
}

.item3 .sub_item1 img {
  width: 30px;
  height: 30px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
  border-radius: 25px;
}

.item3 .sub_item1 span {
  margin-left: 15px;
  display: inline-block;
  height: 30px;
  color: rgb(132, 132, 141);
}

.item3 .sub_item2 {
  margin-left: 45px;
  width: 94%;
  font-size: 14px;
}

/* 一级评论回复框 */
.reply_box {
  width: 91%;
  margin-left: 55px;
  margin-bottom: 15px;
}

/* 修改部分mavon-editor css */
div /deep/ .hljs {
  color: black;
  background: #f6f8fa;
}
</style>