import request from '@/utils/request'

export default {
    searchKey(key,currentPage){
        return request({
            url: "/kkc/search",
            method: 'get',
            params: {
                currentPage,key
            }
        })
    },
    searchTag(id,currentPage){
        return request({
            url: "/kkc/tag/search",
            method: 'get',
            params: {
                currentPage,id
            }
        })
    },
}