<template>
  <div>
    <Header></Header>
    <div class="main">
      <div class="article_nav">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          active-text-color="#3EAF7C"
          @select="handleSelect"
        >
          <el-menu-item
            v-for="data in categoryList"
            :key="data.id"
            :index="data.id"
            >{{ data.label }}</el-menu-item
          >
        </el-menu>
      </div>
      <div class="article_sub_nav" v-show="activeIndex != 1">
        <span
          class="sub_tag"
          v-for="(data, index) in subTagList"
          :key="index"
          @click="subTagClick(data.id)"
        >
          <a href="javascript:void(0);">
            <!-- 在路由判断 是否有点击过，如果有则显示点击后的效果，没有则无效果 -->
            <span class="active" v-if="$route.query.tagId == data.id">{{
              data.label
            }}</span>
            <span v-else>{{ data.label }}</span>
          </a></span
        >
      </div>
      <div class="article_body">
        <el-row>
          <el-col :span="18">
            <div class="article_condition">
              <el-menu
                :default-active="form.condition"
                class="el-menu-demo"
                mode="horizontal"
                active-text-color="#3EAF7C"
                @select="handleSelect2"
              >
                <el-menu-item
                  v-for="data in conditionList"
                  :key="data.index"
                  :index="data.index"
                  >{{ data.category }}</el-menu-item
                >
              </el-menu>
            </div>
            <div class="article_main" v-for="data in datas" :key="data.id">
              <div v-if="data.firstImg != null" class="article_main_body">
                <p class="article-title">
                  <router-link target="_blank" :to="'/article/' + data.id">{{
                    data.title
                  }}</router-link>
                </p>
                <p class="article-tag">
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.official == 1">官方</el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.top == 1">置顶</el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.essence == 1">精华</el-tag>
                  <el-tag
                    v-for="tag in data.tagList"
                    :key="tag.id"
                    type="info"
                    size="mini"
                    ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                      tag.label
                    }}</router-link></el-tag
                  >
                </p>
                <p class="article-describe">
                  <router-link target="_blank" :to="'/article/' + data.id">
                    {{ data.content }}</router-link
                  >
                </p>
                <p class="article-other">
                  <img
                    :src="
                      data.authorAvatar == null ? userImg : data.authorAvatar
                    "
                  />
                  <span
                    ><router-link target="_blank" :to="'/user/' + data.authorId"
                      >{{ data.authorUsername }}&nbsp;&nbsp;</router-link
                    ></span
                  >
                  <span>{{ data.publishTime.split("T")[0] }}</span>
                  <span
                    ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                      data.views
                    }}</span
                  >
                </p>
              </div>
              <div v-else class="article_main_body_noImg">
                <p class="article-title">
                  <router-link target="_blank" :to="'/article/' + data.id">{{
                    data.title
                  }}</router-link>
                </p>
                <p class="article-tag">
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.official == 1">官方</el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.top == 1">置顶</el-tag>
                  <el-tag size="small" style="color: rgb(62, 175, 124);" color="white" v-if="data.essence == 1">精华</el-tag>
                  <el-tag
                    v-for="tag in data.tagList"
                    :key="tag.id"
                    type="info"
                    size="mini"
                    ><router-link target="_blank" :to="'/tag/' + tag.id">{{
                      tag.label
                    }}</router-link></el-tag
                  >
                </p>
                <p class="article-describe">
                  <router-link target="_blank" :to="'/article/' + data.id">
                    {{ data.content }}</router-link
                  >
                </p>
                <p class="article-other">
                  <img
                    :src="
                      data.authorAvatar == null ? userImg : data.authorAvatar
                    "
                  />
                  <span
                    ><router-link target="_blank" :to="'/user/' + data.authorId"
                      >{{ data.authorUsername }}&nbsp;&nbsp;</router-link
                    ></span
                  >
                  <span>{{ data.publishTime.split("T")[0] }}</span>
                  <span
                    ><i class="el-icon-view"></i>&nbsp;&nbsp;{{
                      data.views
                    }}</span
                  >
                </p>
              </div>
              <div v-if="data.firstImg != null" class="article_main_img">
                <router-link target="_blank" :to="'/article/' + data.id"
                  ><img :src="data.firstImg"
                /></router-link>
              </div>
            </div>
            <div class="loading">{{ noMore ? "没有更多了" : "加载中..." }}</div>
          </el-col>
          <el-col :span="6">
            <div :style="{ height: asideHeight + 'px' }" class="article_aside">
              <div class="article_bulletin_area">
                <div class="article_bulletin_tip">
                  <span>公告</span>
                </div>
                <div class="article_bulletin_list">
                  <div
                    class="article_bulletin_node"
                    v-for="value in 4"
                    :key="value"
                  >
                    <el-row>
                      <el-col :span="16"
                        ><span class="title"
                          >123123123123123123123123123123</span
                        ></el-col
                      >
                      <el-col :span="8"
                        ><span style="color: rgb(132, 132, 141)"
                          >2023-01-02</span
                        ></el-col
                      >
                    </el-row>
                  </div>
                </div>
              </div>
              <div class="article_random_area">
                <div class="article_random_tip">
                  <span>随便看看</span>
                </div>
                <div
                  class="article_random_node"
                  v-for="value in 5"
                  :key="value"
                >
                  <p class="article_random_title">
                    <router-link target="_blank" :to="'/article/' + value"
                      >aaaaaaaaaaaaa111111111111111111111</router-link
                    >
                  </p>
                  <p class="article_random_other">
                    <span
                      ><img
                        src="@/assets/images/nologin.png"
                      />&nbsp;<router-link target="_blank" to="/user/123123"
                        >作者123123</router-link
                      ></span
                    >
                    <span> <i class="fa-regular fa-thumbs-up"></i> 1000</span>
                  </p>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import common from "@/api/common";

export default {
  name: "ArticleList",
  data() {
    return {
      activeIndex: "1",   //分类点击索引
      categoryList: [],   //分类数据列表
      conditionList: [    //条件列表
        { index: "10000", category: "最新" },
        { index: "10001", category: "最热" },
      ],
      subTagList: ["aaa"],  //子标签数据列表
      datas: [],    //文章数据存储列表
      isAchiveBottom: false, //滚动条是否到底部标
      noMore: false,    //是否到底
      userImg: require("@/assets/images/nologin.png"),  //当用户没有头像则使用这个
      asideHeight: 170,   //自适应高度，实现sticky能跟随
      relationData: {},   //存储分类和子标签的 集合，key:分类id value:子标签列表 减少分类点击后的操作
      form: {     //条件参数
        categoryId: 1,    //分类id
        tagId: 1,   //标签id
        currentPage: 1,   //当前页
        condition: "10000", //条件id
      },
    };
  },
  created() {
    //使用window.onscroll = function(){}this指向出现问题
    //故应该使用箭头函数，因为箭头函数无this，会从上一级找，故会找到vue实例的this
    window.onscroll = () => {
      //变量scrollTop是滚动条滚动时，距离顶部的距离
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //变量windowHeight是可视区的高度
      var windowHeight =
        document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      var scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;

      // if (this.datas.length >= 10) {
      //   this.noMore = true;
      // }
      //滚动条到底部的条件(距底部20px时触发加载)
      console.log(
        scrollTop + windowHeight >= scrollHeight - 20 &&
          !this.isAchiveBottom &&
          !this.noMore
      );
      if (
        scrollTop + windowHeight >= scrollHeight - 20 &&
        !this.isAchiveBottom &&
        !this.noMore
      ) {
        // console.log(
        //   "距顶部" +
        //     scrollTop +
        //     "可视区高度" +
        //     windowHeight +
        //     "滚动条总高度" +
        //     scrollHeight
        // );
        this.isAchiveBottom = true;
        //延时触发数据加载
        setTimeout(() => {
          //后端需要进行分页，然后前端从后端拿来实现滚动加载
          //这里利用数组push来模拟从后端拿到的数据
          this.form.currentPage = this.form.currentPage + 1;
          this.getArticleList();
          this.isAchiveBottom = false;
        }, 500);
      }
    };
  },
  beforeMount() {
    // 在页面挂载前就发起请求
  },
  mounted() {
    this.getCategoryTree();
    this.getArticleList();
  },
  methods: {
    getArticleList() {
      //获取文章列表
      //如果是第一页，则直接赋值到datas
      if (this.form.currentPage == 1) {
        common.getArticleListByCondition(this.form).then((resp) => {
          this.datas = resp.data.data;
          this.asideHeight = this.asideHeight + this.datas.length * 170;
        });
      } else {
        //如果不是第一页，则追加到datas中
        let length = this.datas.length;
        common.getArticleListByCondition(this.form).then((resp) => {
          this.datas = this.datas.concat(resp.data.data);
          //如果数量小于10，说明后面已经没有数据了，则设置noMore = true
          if (resp.data.data.length < 10) {
            this.noMore = true;
          }
          this.asideHeight =
            this.asideHeight + (this.datas.length - length) * 170;
        });
      }
    },
    handleSelect(index) {
      //TODO 选中分类标签功能
      //1、根据选中的索引 找出对应的 子标签 放入到subTagList中

      //初始化参数
      this.activeIndex = index;
      this.form.categoryId = index; //修改分类id
      this.form.tagId = 1;  //修改子标签id为1
      this.form.currentPage = 1;  //设置当前页为1
      this.form.condition = "10000";  //设置条件为10000 最新
      this.asideHeight = 170; //自适应高度初始值
      //设置子标签要显示的数据
      this.subTagList = this.relationData[index];
      //文章列表数据为空
      this.datas = [];
      this.$router.push({
        path: "/article",
        query: {
          categoryId: index,
          tagId: 1,
        },
      });
      this.getArticleList();
    },
    subTagClick(tagId) {
      //子标签点击时触发的事件
      //赋值
      let categoryId = this.$route.query.categoryId;
      this.form.categoryId = categoryId;
      this.form.tagId = tagId;
      this.asideHeight = 170;
      this.form.currentPage = 1;

      this.$router.push({
        path: "/article",
        query: {
          categoryId,
          tagId,
        },
      });
      this.getArticleList();
    },
    handleSelect2(index) {
      //条件点击时触发事件
      this.asideHeight = 170;
      this.form.condition = index;
      this.getArticleList();
    },
    getCategoryTree() {
      //获取分类树，将数据再导航栏中显示
      common.getCategoryTree().then((resp) => {
        let relationData = resp.data.data;
        this.categoryList.push({ id: "1", label: "全部" });
        for (let i = 0; i < relationData.length; i++) {
          this.categoryList.push({
            id: relationData[i].id + "",
            label: relationData[i].label,
          });
          this.relationData[relationData[i].id] = relationData[i].children;
          this.relationData[relationData[i].id].unshift({
            id: "1",
            label: "全部",
          });
        }
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
  color: black;
}

/* 整个首页的主体 */
.main {
  margin: 0 auto;
  margin-top: 70px;
  width: 1200px;
}

/* 文章页 大标签栏 */
.article_nav {
  width: 100%;
  background-color: white;
  margin-bottom: 10px;
}

.article_nav span {
  display: inline-block;
  height: 100%;
  width: 100px;
}

/* 对标签栏个体高度进行修改 */
.el-menu-item {
  height: 50px;
  line-height: 50px;
}

/* 文章页 点击大标签栏后的 小标签 */
.article_sub_nav {
  width: 100%;
  margin-bottom: 10px;
}

.sub_tag {
  margin-top: 10px;
  margin-right: 10px;
  padding-left: 9px;
  padding-right: 9px;
  padding-top: 5px;
  /* padding-bottom: 2px; */
  display: inline-block;
  height: 24px;
  /* background-color: rgb(62, 175, 124); */
  background-color: white;
  font-size: 13px;
  border-radius: 8px;
}

/* 文章页 列表主题(包括文章主题和侧边主题) */
.article_body {
  width: 100%;
  /* position: sticky; */
}

/* 公告栏主体 */
.article_bulletin_area {
  width: 100%;
  height: 300px;
  background-color: white;
}

/* 侧边主题： 公告/随便看看 标题 */
.article_bulletin_tip,
.article_random_tip {
  height: 55px;
  width: 100%;
  /* background-color: green; */
  line-height: 55px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 公告栏/随便看看 标题 */
.article_bulletin_tip span,
.article_random_tip span {
  margin-left: 15px;
  font-size: 18px;
  font-weight: bolder;
}

.article_bulletin_list {
  width: 100%;
  height: 100%;
}

.article_bulletin_node {
  height: 47px;
  width: 100%;
  line-height: 47px;
  font-size: 14px;
}

.article_bulletin_node .title {
  display: inline-block;
  width: 80%;
  margin-left: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 随便看看 主体 */
.article_random_area {
  position: sticky;
  top: 70px;
  /* bottom: 0; */
  margin-top: 20px;
  width: 100%;
  height: 460px;
  background-color: white;
}

/* 随便看看 文章栏 */
.article_random_node {
  height: 80px;
  width: 100%;
  /* background-color: yellow; */
}

/* 标题 */
.article_random_title {
  padding-top: 18px;
  margin-left: 13px;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* 其他部分 */
.article_random_other {
  margin-top: 10px;
}

/* 其他内容 */
.article_random_other span {
  margin-left: 13px;
  display: inline-block;
  /* width: 30%; */
  font-size: 13px;
}

.article_random_other span img {
  width: 28px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
}

.article_random_other span:last-child {
  width: 30%;
  float: right;
  /* background-color: yellow; */
  margin-top: 5px;
}

.article_condition {
  width: 98%;
  background-color: white;
  height: 40px;
  border-bottom: 1px solid rgb(247, 247, 252);
}

/* 对条件栏个体高度进行修改 */
.article_condition .el-menu-item {
  height: 40px;
  line-height: 40px;
}

/* 文章栏主体 */
.article_main {
  display: flex;
  width: 98%;
  height: 170px;
  background-color: white;
  border-bottom: 1px solid rgb(247, 247, 252);
}

.article_main:hover {
  background-color: rgb(238, 253, 238);
  cursor: pointer; /* 当移动到指定位置时 鼠标样式发生变化 */
}

/* 自动滚动 底部栏主题 */
.loading {
  width: 98%;
  height: 60px;
  background-color: white;
  text-align: center;
  line-height: 60px;
  color: rgb(132, 132, 141);
}

/* 文章栏 文字显示部分 */
.article_main_body {
  display: inline-block;
  width: 80%;
  height: 100%;
  /* background-color: rgb(93, 177, 93); */
}

.article_main_body_noImg {
  display: inline-block;
  width: 100%;
  height: 100%;
  /* background-color: rgb(93, 177, 93); */
}

.article_main_img {
  display: inline-block;
  text-align: center;
  width: 20%;
  height: 100%;
  /* background-color: rgb(185, 185, 117); */
}

.article_main_img img {
  width: 90%;
  height: 90%;
  margin-top: 16px;
}

/* 标题 */
.article-title {
  width: 95%;
  margin-top: 13px;
  margin-left: 13px;
  font-size: 21px;
  text-overflow: ellipsis; /* 文字超出部分变成省略号 */
  overflow: hidden; /* 文字超出隐藏*/
  white-space: nowrap; /* 不会换行，继续在同一行文字输出*/
  font-weight: bolder; /* 加粗*/
}

.article-title a:hover,
.answer-title a:hover,
.article_random_title a:hover {
  color: rgb(62, 175, 124);
}

/* 标签 */
.article-tag {
  margin-left: 28px;
  margin-top: 5px;
  margin-bottom: 15px;
}

.article-tag .el-tag {
  margin-right: 5px;
}

/* 描述 */
.article-describe {
  width: 95%;
  height: 60px;
  margin-left: 13px;
  font-size: 14px;
  color: gray;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;

  margin-top: -8px;
}

.article-describe a {
  color: gray;
}

/* 其他 */
.article-other {
  /* margin-top: 8px; */
  margin-left: 13px;
  color: black;
}

.article-other img {
  margin-right: 5px;
  width: 30px;
  height: 30px;
  vertical-align: middle; /* 与旁边的文字居中对齐 */
  border-radius: 30px;
}

.article-other span {
  display: inline-block;
  /* width: 100px; */
  margin-right: 20px;
  font-size: 14px;
  /* border-right: 1px solid grey; */
}

.article-other span:last-child {
  border-right: 0px;
}
</style>