<template>
  <div class="header">
    <el-row>
      <el-col :span="5"
        ><div class="header_logo">
          <router-link to="/">AATTS</router-link>
        </div></el-col
      >
      <el-col :span="7"
        ><div class="header_nav">
          <router-link to="/article">文章</router-link>
          <router-link to="/question">问答</router-link>
          <a @click="articleWriteClick"
            ><i class="el-icon-edit-outline"></i>&nbsp;写文章</a
          >
          <a @click="questionWriteClick"
            ><i class="el-icon-edit-outline"></i>&nbsp;提问题</a
          >
        </div></el-col
      >
      <el-col :span="8"
        ><div class="header_search">
          <el-input
            style="height: 20px"
            id="nav_search"
            v-model="input"
            placeholder="请输入你想搜索的文章或问题"
          >
            <i
              slot="suffix"
              class="el-icon-search search_button"
              @click="searchClick"
            ></i>
          </el-input></div
      ></el-col>
      <el-col :span="4">
        <div class="header_user">
          <router-link target="_blank" :to="'/user/' + id"
            ><img :src="avatar_url"
          /></router-link>
          <span v-if="username.length == 0"
            ><router-link to="/login">请登录</router-link></span
          >
          <span v-else>
            <el-popover trigger="hover">
              <el-button @click="logout" type="text">退出登录</el-button>
              <template slot="reference"
                ><router-link target="_blank" :to="'/user/' + id">{{
                  username
                }}</router-link></template
              >
            </el-popover>
          </span>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import common from "@/api/common";
import cookie from "js-cookie";

export default {
  data() {
    return {
      id: "",
      input: "",
      avatar_url: require("@/assets/images/nologin.png"),
      username: "",
    };
  },
  mounted() {
    this.getUserInfo();
    //绑定loginRefresh事件，当触发时 请求用户信息
    this.$bus.$on("loginRefresh", this.getUserInfo);
  },
  methods: {
    getUserInfo() {
      let token = cookie.get("token");
      if(token === undefined){
        return ;
      }
      common.getUserInfo().then((resp) => {
        let data = resp.data.data;
        this.id = data.id;
        if (data.avatar != null) {
          this.avatar_url = data.avatar;
        }

        this.username = data.username;
      });
    },
    searchClick() {
      if (this.input.length > 0) {
        this.$router.push({
          path: "/search/" + this.input,
        });
        this.$emit("refreshSearchList");
      }
    },
    logout() {
      common.logout().then(() => {
        this.$message({
          message: "退出成功",
          type: "success",
        });
        cookie.remove("token");
        this.id = "";
        this.avatar_url = require("@/assets/images/nologin.png");
        this.username = "";
      });
    },
    articleWriteClick() {
      if (cookie.get("token")) {
        this.$router.push({ path: "/article/write" });
      } else {
        this.$message({
          message: "请先登陆",
          type: "warning",
        });

        this.$router.push({ path: "/login" });
      }
    },
    questionWriteClick() {
      if (cookie.get("token")) {
        this.$router.push({ path: "/question/write" });
      } else {
        this.$message({
          message: "请先登陆",
          type: "warning",
        });

        this.$router.push({ path: "/login" });
      }
    },
  },
  beforeDestory() {
    this.$bus.$off("loginRefresh");
  },
};
</script>

